<template>
   <div class="toolbar" style="margin:10px;">
     <!--工具条-->
     <div class="cxRegAmount">
      <el-col :span="18" class="toolbar">
       <h3>成型机台定额(常规生产8小时/班)</h3>
      </el-col>
      <el-col :span="6" style="padding-top:20px">
        <el-button type="primary" @click="handleCxAdd" size="mini">新增</el-button>
      </el-col>

      <!-- 查询数据产量定额显示 序号，机台，牌号，定额（这里表示的是常规生产8小时/班的定额）-->
      <el-table ref="table" :data="regCxAmounts.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width:80%">
       <el-table-column prop="id" label="序号"  sortable>
        </el-table-column>
        <el-table-column prop="machine" label="机台">
        </el-table-column>
        <el-table-column prop="brandType" label="牌号">
        </el-table-column>
        <el-table-column prop="amount" label="定额">
        </el-table-column>
        <el-table-column label="操作">
           <template scope="scope">
          <el-button type="primary" @click="handleCxEdit(scope.$index,scope.row)" size="mini">修改</el-button>
          <el-button type="danger" @click="handleCxDelete(scope.$index,scope.row)" size="mini">删除</el-button>
        </template>
        </el-table-column>
      </el-table>
       <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 20, 40]" 
            :page-size="pagesize"         
            layout="total, sizes, prev, pager, next, jumper"
            :total="this.regCxAmounts.length">
      </el-pagination>
      </div>
  
    <!--新增编辑界面-->
  <el-dialog :title="cxOperation?'新增':'编辑'" width="50%" :visible.sync="cxDataFormVisible" :close-on-click-modal="false">
    <el-form :model="cxDataForm" label-width="120px" :rules="cxDataFormRules" ref="cxDataForm" :size="size">
       <el-form-item label="机台">
          <el-input v-model="cxDataForm.machine"></el-input>
        </el-form-item>
        <el-form-item label="牌号">
           <el-input v-model="cxDataForm.brandType"></el-input>
        </el-form-item>
        <el-form-item label="定额(万只/班)">
          <el-input v-model="cxDataForm.amount"></el-input>
        </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button :size="size" @click.native="cxDataFormVisible = false">取消</el-button>
      <el-button :size="size" type="primary" @click.native="submitCxForm" :loading="cxEditLoading">提交</el-button>
    </div>
  </el-dialog>
      </div>
    </div>
   </div>
</template>

<script>
  export default {

    name: 'RegOutputAmount',
    data () {
    return {
      size: 'small',
      cxOperation: false, // true:新增, false:编辑
      cxEditLoading: false,
      
      // 新增卷包定额数据
      //新增界面数据
        cxDataForm: {
          machine:'',
          brandType:'',
          amount:''
        },
      cxDataFormVisible: false,//新增界面是否显示
      cxDataFormRules: {
        amount: [
          { required: true, message: '填写不能为空', trigger: 'blur' }
        ]
      },
     //分页
      currentPage:1, //初始页
      pagesize:5,    //每页的数据
      // 卷包机台定额
      regCxAmounts:[],
      
    }
  },
  methods:{

    // 获取全部数据
    findCxAll: function (data) {
      
      this.$api.regAmount.findAll().then((res) => {
        this.regCxAmounts = res.data
        console.log(this.regCxAmounts)
      })
    }, 

    // 删除
    handleCxDelete: function (index,row) {
      this.$confirm('确认删除选中记录吗？', '提示', {
        type: 'warning'
      }).then(() => {
          this.cxDelete(row.id)
      })
    },
    // 删除操作
    cxDelete: function (ids) {
     
        let params = []
        let idArray = (ids+'').split(',')
        for(var i=0; i<idArray.length; i++) {
          params.push({'id':idArray[i]})
        }
        this.loading = true
        let callback = res => {
          if(res.code == 200) {
            this.$message({message: '删除成功', type: 'success'})
          } else {
            this.$message({message: '操作失败, ' + res.msg, type: 'error'})
          }
          this.loading = false
        }
         this.$api.regAmount.batchDelete(params)
      },

     // 显示新增界面
    handleCxAdd: function () {
      this.cxDataFormVisible = true
      this.cxOperation = true
      this.cxDataForm = {
        id: 0,
        machine: '',
        brandType: '',
        amount:''
      }
    },

    handleCxEdit:function(index, row)
    {
      this.cxDataFormVisible = true
      this.cxOperation = false
      this.cxDataForm = Object.assign({}, row)
    },
    // 编辑
    submitCxForm: function () {
      this.$refs.cxDataForm.validate((valid) => {
        if (valid) {
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            this.cxEditLoading = true
            let params = Object.assign({}, this.cxDataForm)
            this.$api.regAmount.save(params).then((res) => {
              this.cxEditLoading = false
              if(res.code == 200) {
                this.$message({ message: '操作成功', type: 'success' })
                this.cxDataFormVisible = false 
                this.$refs['cxDataForm'].resetFields()
              } else {
                this.$message({message: '操作失败, ' + res.msg, type: 'error'})
              }
              this.findAll()
            })
          })
        }
      })
    },

 
    // 初始页currentPage、初始每页数据数pagesize和数据data
    handleSizeChange:function(size) {
      this.pagesize = size;
      console.log(this.pagesize)  //每页下拉显示数据
    },
    handleCurrentChange:function(currentPage){
      this.currentPage = currentPage;
      console.log(this.currentPage)  //点击第几页
    },
  
  
    // 查询产量定额信息
    getCxRegOutput:function()
    {
      this.$http.get("http://localhost:3000/regAmount")
          .then(function(response){
            
            this.regCxAmounts = response.body;
            console.log( this.regCxAmounts);
          })
    }
  },
  created()
  {
    this.getCxRegOutput();
  }
  }
</script>

<style lang="scss">
  .site-wrapper.site-page--not-found {
    position: absolute;
    top: 60px;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    .site-content__wrapper {
      padding: 0;
      margin: 0;
      background-color: #fff;
    }
    .site-content {
      position: fixed;
      top: 15%;
      left: 50%;
      z-index: 2;
      padding: 30px;
      text-align: center;
      transform: translate(-50%, 0);
    }
    .not-found-title {
      margin: 20px 0 15px;
      font-size: 8em;
      font-weight: 500;
      color: rgb(55, 71, 79);
    }
    .not-found-desc {
      margin: 0 0 30px;
      font-size: 26px;
      text-transform: uppercase;
      color: rgb(118, 131, 143);
      > em {
        font-style: normal;
        color: #ee8145;
      }
    }
    .not-found-btn-gohome {
      margin-left: 30px;
    }
  }
</style>



