<template>
  <div class="container">  
     <div  style="float:left;padding-top:5px;">
      <el-form ref="form" :model="filter" :span="100" label-width="200px">
        <el-form-item label="生产牌号：" prop="brand">
         <el-select v-model="filter.brand" placeholder="请选择生产牌号">
           <el-option v-for="item in brands"
             :label="item.name"
             :key="item.name"
             :value="item.name">
           </el-option>
         </el-select>
       </el-form-item>

       <el-form-item label="机台号：" prop="machineno">
       <el-select  v-model="filter.mid" placeholder="请选择机台号">
         <el-option v-for="item in machinenos"
           :label="item.label"
           :key="item.id"
           :value="item.id">
         </el-option>
         </el-select>
         
       </el-form-item>
       <el-form-item label="实际单价（万支）：" prop="price">
        <span style="font-size:30px;align:left">{{this.price}}</span>
        </el-form-item>

       <el-form-item label="产量（万支）：" placeholder="请输入今日总产量" prop="amount">
        <el-input v-model="filter.amount"></el-input>
       </el-form-item>
       <el-form-item label="参考产量薪资：" prop="salary">
        <span style="color:red;font-size:30px;align:left">{{this.total}}</span></el-form-item>
       <el-form-item>
         <el-button type="primary" @click="calSalary">查询</el-button>
          <el-button  @click="resetForm" >重置</el-button>
       </el-form-item>

      </el-form>
     </div>
  </div>

</template>

<script>
import Cookies from "js-cookie";
import store from '@/store'
export default {
  name: 'SalaryAmount',

  data () {
    return {
      userName:"",
      userPwd:"",
      price:'0',
      total:"0.0",

      // 通用filter
      filter:{
        name:'', // 用户名 
        brand:'',
        mid:'',
        amount:''
      },
      // 分页
      currentPage:1, //初始页
      pagesize:5,    //每页的数据

      // /////////需要补充从json后台获取的数据返回
    machinenos :[
      {"id":"1","label":"1#"},
      {"id":"2","label":"2#"},
      {"id":"3","label":"3#"},
      {"id":"4","label":"4#"},
      {"id":"5","label":"5#"},
      {"id":"6","label":"6#"},
      {"id":"7","label":"7#"},
      {"id":"8","label":"8#"},
      {"id":"9","label":"9#"},
      {"id":"10","label":"10#"},
      {"id":"11","label":"11#"},
      {"id":"12","label":"12#"},
      {"id":"13","label":"13#"},
      {"id":"14","label":"14#"},
      {"id":"15","label":"15#"},
      {"id":"16","label":"16#"},
      {"id":"17","label":"17#"},
      {"id":"18","label":"18#"},
      {"id":"19","label":"19#"},
      {"id":"20","label":"20#"},
      {"id":"21","label":"21#"},
      {"id":"22","label":"22#"},
      {"id":"23","label":"23#"},
      {"id":"24","label":"24#"},
      {"id":"25","label":"25#"},
      {"id":"26","label":"26#"},
      {"id":"27","label":"27#"},
      {"id":"28","label":"28#"},
      {"id":"29","label":"29#"},
      {"id":"30","label":"30#"} ],
      // /////////需要补充从json后台获取的数据返回
      brands:[
              {"id":"1","name":"利群（新版）"},
              {"id":"2","name":"大红鹰（软蓝）"},
              {"id":"4","name":"真龙（神韵）"},
              {"id":"5","name":"真龙（海韵）"},
              {"id":"6","name":"真龙（海韵细支）"},
              {"id":"8","name":"真龙（鸿韵）"},
              {"id":"9","name":"真龙（佳韵）"},
              {"id":"10","name":"真龙（龙天下）"},
              {"id":"11","name":"真龙（起源）"},
              {"id":"12","name":"真龙（美人香草）"},
              {"id":"13","name":"真龙（壮丽）"},
              {"id":"14","name":"真龙（晶钻刘三姐）"},
              {"id":"16","name":"真龙（软海韵）"},
              {"id":"17","name":"真龙（凌云）"},
              {"id":"18","name":"真龙（致青春）"},
              {"id":"19","name":"真龙（珍品）"},
              {"id":"21","name":"真龙（软娇子）"},
              {"id":"22","name":"真龙（娇子）"}],
        loading: false,
      
    }
  },
  methods:{
    initFilter:function()
    {
      this.filter.name = sessionStorage.getItem('user');
    },
    calPrice:function(){
      if(this.filter.brand && this.filter.mid)
      {
        this.initFilter();
        // 计算当前单价
        this.$api.amtCounter.getUnitPrice(this.filter).then((res) => {
          // 处理异常
          if(res.msg)
          {
            this.$message({message: '操作失败, ' + res.msg, type: 'error'})
          }
         console.log(res.data)
          this.price = res.data
      })
      }
    },
    calSalary:function()
    {
      if(!this.filter.brand)
      {
        this.$message({message: '请先选择牌号',type: 'error'})
      }
      else if(!this.filter.mid)
      {
        this.$message({message: '请先选择机台号',type: 'error'})
      }
      else if(!this.filter.amount)
      {
        this.$message({message: '请输入总产量', type: 'error'})
      }
      this.total = this.filter.amount * this.price;
      console.log(this.filter.amount)
    },
    
    resetForm(){
      this.$refs.form.resetFields()
    },
     // 初始页currentPage、初始每页数据数pagesize和数据data
    handleSizeChange:function(size) {
      this.pagesize = size;
      console.log(this.pagesize)  //每页下拉显示数据
    },
    handleCurrentChange:function(currentPage){
      this.currentPage = currentPage;
      console.log(this.currentPage)  //点击第几页
    }

  },
  created(){
  },
  updated(){
    this.calPrice();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.el-table__expand-column .cell {
  display: none;
}
.el-footer {
    background-color:  #f8f8f8;
    color: #333;
    font-size: x-large;
    text-align:center;
  }


</style>



