<template>
  <div class="container">
  <!--工具栏-->
      <div class="searchbox">
      <el-autocomplete
           v-model="accountSearch"
           :fetch-suggestions="accounQuerySearchAsync"
           @select="accountHandleSelect"
           :debounce=10
            placeholder="请输入姓名"
       >
      </el-autocomplete>
      <el-button @click="accountFind">搜索</el-button>
    </div>
  </div>
</template>

<script>
import KtTable from "@/views/Core/KtTable"
import KtButton from "@/views/Core/KtButton"
import TableTreeColumn from '@/views/Core/TableTreeColumn'
import { format } from "@/utils/datetime"
export default {
 
  data() {
    return {
       accountSearch: "",
       options:[],
       empPostInfos:[]         
    }
  },
  methods: {
    getInitData:function()
    {
       this.$http.get("http://localhost:3000/empPostInfo")
          .then(function(response){
             console.log(response.body);
            this.empPostInfos = response.body;
             this.options = this.empPostInfos;

          })
    },
   //queryString 为在框中输入的值    callback 回调函数,将处理好的数据推回
    accounQuerySearchAsync(queryString, callback) {
         let list = [{}];
         let param = {
              pageNum: 1,
              pageSize: 9999,
              key: queryString
         }      
         
//      let data = this.getAccountUserInfo(param,0);
        this.options.forEach(function(item,i){
              if(item.name.indexOf(queryString) != -1){
                    list.push({"value":item.name});   
              }      
        })
        if(!queryString){
              list = list.splice(0,7);
        }
        callback(list);
    },
    // 模糊查询下拉框 鼠标键盘选中点击触发
    accountHandleSelect(item) { 
         this.accountSearch = item.value;
    },     
    accountFind: function(){
         if(!this.accountSearch){
               this.$message({ type: 'warning', message: "请输入要查询的内容！"});
                     return;
               }
          let param = {
               pageNum: 1,
               pageSize: this.accountPagesize,
               key: this.accountSearch
          }
          this.getAccountUserInfo(param,1);
    },
    getAccountUserInfo: function(param,status){
         findUsersAndDepartment(param).then((data) => {
               if(data.ret.succeed){
                     if(status == 0){             // 模糊查询
                           return data;
                      }else if(status == 1) {       // 获得表格数据
                           this.$message({
                                type: 'info',
                                 message: data.ret.retMsg
                            });
                                 this.accountList = data.page.list;
                      }
               }      
         })

   }
  },
  created(){
    this.getInitData()
  },
  mounted() {
  }
}
</script>
<style scoped>
.menu-container {
  margin-top: 10px;
}
.menu-header {
  padding-left: 8px;
  padding-bottom: 5px;
  text-align: left;
  font-size: 16px;
  color: rgb(20, 89, 121);
  
}
</style>


