 <template>
  <div class="book-container">  
   <span style="color:red">请先选择预约会议室：
     <el-select v-model="roomId" placeholder="请选择会议室">
      <el-option
      v-for="item in roomOptions"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
</span>
<vue-scheduler 
:time-range="[8,20]"
:available-views="['week', 'day']"
initial-view="day"
:initial-date="new Date()"
:events="events"
:event-display="eventDisplay"
:show-today-button="true"
:event-dialog-config="dialogConfig"
@event-clicked="eventClicked"
@event-created="eventCreated"/>

<el-dialog title="会议预定详情" :visible.sync="dialogTableVisible" width="100%">

</el-dialog>
</div>
</template>




<script>
  export default {
    data() {
      return {
        
        // 初始时间
        initDate:"",
        dialogTableVisible:false,
        weekChangedFlag:false,
        weekDate:'',
        temp:[],
        events: [],
        roomId:"1",// 默认值
        filter:{},
        roomString:'',
        roomOptions:[
        {value:'1',label:'卷包车间党员之家'},
        {value:"2",label:"卷包车间会议室"},
        {value:"3",label:"卷包车间二楼大师工作室"}],
        // 会议室305 会议主题 日期、开始时间、结束时间、预约人、联系电话、备注
        dialogConfig: {
          title: '预定会议室',
          createButtonLabel: '预约',
          enableTimeInputs: true,

          fields: [
          {
            name: 'topic',
            required: true,
            label: '会议主题',
            value:''
          },
          {
            name: 'createUser',
            required: true,
            label: '预约人',
            value:''
          },
          {
            name: 'telephone',
            required: true,
            label: '预约人电话'
          },
          {
            name: 'comments',
            type: 'textarea',
            label: '备注说明'
          }
          ]
        }
      }
    },
    methods: {

        // 初始化Events
        initEvents(queryDate)
        {
          this.initRoomString();
          // 根据RoomId,查询events
          this.queryReserve(queryDate)
        },
        queryReserve(queryDate)
        {
          var queryDate = this.$moment(queryDate).format('YYYY-MM-DD');
          console.log("初始化events...."+queryDate)
          console.log("查询一周预定events...."+this.roomId)
          this.filter.roomId = this.roomId;
          this.filter.queryDate = queryDate;
          // 获取全部数据
          this.$api.bookRoom.findByRoom(this.filter).then((res) => {
            this.events = res.data
            console.log(this.events)
          })
        },
        initRoomString()
        {
          var num = this.roomId-1;
          console.log(this.roomOptions[num].label);
          this.roomString = "预定"+this.roomOptions[num].label;
          this.dialogConfig.title = this.roomString;
        },

        eventClicked(event) {

          console.log('Event clicked');

          console.log(event);

        },
        checkReserveTime(event)
        {
          var start = event.startTime;
          var end = event.endTime;
          var startSuffix = start.substring(start.length-2,start.length);
          var endSuffix = end.substring(end.length-2,end.length);
          console.log("check suffix" + startSuffix + endSuffix)
          if('00'== startSuffix || '30' == startSuffix)
            if('00'== endSuffix || '30' == endSuffix)
            {
              return true;
            }
            return false;
          },

          // 构建后台保存预定数据对象
          buildReserveObj(event){
            // 添加roomId属性
            event.roomId = this.roomId
            // 添加dateNo属性
            let dateNo = this.$moment(event.date).format('YYYY-MM-DD');
            event.dateNo = dateNo
            console.log(event)
            return event;
          },
        // add 创建====
        eventCreated(event) {
          console.log('预约成功！');
          // 输入时间校验
          if(this.checkReserveTime(event))
          {
            // 构造后台保存数据
            this.buildReserveObj(event)
            this.$api.bookRoom.save(event).then((res)=>
            {
              if(res.code == 200) {
                this.$message({ message: '预约成功', type: 'success' })
              } else {
                this.$message({message: '预约失败：' + res.msg, type: 'error'})
               this.removeLast(); // events数组中删除最后新增预定
             }
           })
          }else{
            this.removeLast(); // events数组中删除最后新增预定
            console.log(this.events.length)
            this.$message({message: '预约时间只能是整点、半点', type: 'error'})
          }
        },
        removeLast()
        {
          console.log("removeLast start:"+this.events.length)
          var last = this.events.pop()
          console.log("removeLast end:"+ last)
        },
        eventDisplay(event) {
         var tmp = "主题："+event.topic +'\n' 
         + "创建人："+event.createUser +'\n' 
         + "联系电话："+event.telephone+'\n';
         if(event.comments != null)
         {
          tmp+= "备注信息："+event.comments;
        }
        var displayValue = tmp;

         // console.log(displayValue);
         return displayValue;
       }
       
      //  dayChanged(newDate) {
      //   console.log('day Changed');
      //   this.initEvents(newDate)
      // },
     
     },
     watch: {
      roomId(val, oldVal) {
        // 重新从后台加载数据
        this.initEvents(new Date()) 
        console.log("roomId = " + val + " , oldValue = " + oldVal)
      }
    },
    created()
    {
      this.initEvents(new Date()) 
    },
    updated() {
      
   }
 }

</script>

<style lang="scss">
  .book-container {
    position: absolute;
    top: 40px;
    bottom: 0px;
    left: 10px;
    right: 0px;
    text-align:left;
    width:85%;
    font-size:large;

  }


  .cell-red{
    background: red;
  }
</style>


