<template>
    <div class="menu-bar-container">
    <!-- logo -->
    <!-- <div class="logo" :class="isCollapse?'menu-bar-collapse-width':'menu-bar-width'"> -->
        <!-- <img :src="this.logo" />  -->
       <!--  <div>{{isCollapse?'':sysName}}</div>
    </div> -->
    <!-- 导航菜单 -->
    <el-menu default-active="1-3"  @open="handleopen" @close="handleclose" @select="handleselect" >
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span slot="title">工资查询</span>
        </template>
        <el-menu-item index="1-1" @click="$router.push('salaryAmount')">产量工资查询</el-menu-item>
        <el-menu-item index="1-2" @click="$router.push('salaryTime')">汇总工资查询</el-menu-item>
          <el-menu-item index="1-3" @click="$router.push('empInfo')">个人信息修改</el-menu-item>
      </el-submenu>
      <el-submenu index="2">
        <template slot="title">
          <i class="el-icon-document"></i>
          <span slot="title">工资管理</span>
        </template>
         <el-menu-item index="2-1" @click="$router.push('worktime')">工时调整</el-menu-item>
          <el-menu-item index="2-2" @click="$router.push('regAmount')">产量定额</el-menu-item>
      </el-submenu>

       <el-submenu index="3">
         <template slot="title">
        <i class="el-icon-setting"></i>
        <span slot="title">系统管理</span>  
         </template>
        <!-- 牌号管理，机台号管理，班组信息（+上班模式） -->
         <el-menu-item index="3-1" @click="$router.push('worktime')">牌号管理</el-menu-item>
          <el-menu-item index="3-2" @click="$router.push('regAmount')">产量定额</el-menu-item>
            <el-menu-item index="3-1" @click="$router.push('worktime')">班组信息</el-menu-item>
     </el-submenu>
   </el-menu>
    </div>
</template>

<script>
import { mapState } from 'vuex'
import mock from "@/mock/index"
export default {
  data() {
    return {
      isCollapse: false,
      sysName: "",
      logo: "",
    };
  },
  methods: {
    handleopen() {
      console.log('handleopen');
    },
    handleclose() {
      console.log('handleclose');
    },
    handleselect(a, b) {
      console.log('handleselect');
    }
  },
  mounted() {
    this.sysName = "卷包工资管理平台";
  }
};
</script>

<style scoped lang="scss">
.menu-bar-container {
  .el-menu {
    position:absolute;
    top: 60px;
    bottom: 0px;
    text-align: left;
  }
 /*  .logo {
   position:absolute;
   top: 0px;
   height: 60px;   
   line-height: 60px;
   background-color:rgba(30,133,240,0.8); 
   background: #4b5f6e;
  
   div {
     margin:10px;
     font-size: 24px;
     color: white;
     text-align: left;
     width:auto;
   }
 } */
  .menu-bar-width {
    width: 200px;
  }
  .menu-bar-collapse-width {
    width: 65px;
  }
}
</style>


