<template>
  <div class="container">  
    <div class="emp-detail" style="float:left;padding-top:5px;">
      <el-form ref="emp" :model="empInfo" :span="100" label-width="120px" style="margin:20px;width:60%;min-width:600px;">
        <el-form-item label="工  号：" >
           <el-input v-model="empInfo.eno"></el-input>
        </el-form-item>
        <el-form-item label="姓名：" >
          <el-input v-model="empInfo.name"></el-input>
        </el-form-item>
        <el-form-item label="姓名：" >
          <el-input v-model="empInfo.name"></el-input>
        </el-form-item>

        <el-form-item label="姓名：" >
          <el-input v-model="empInfo.name"></el-input>
        </el-form-item>
        <el-form-item label="姓名：" >
          <el-input v-model="empInfo.name"></el-input>
        </el-form-item>
        <el-form-item label="班组：" >
          <el-select v-model="empInfo.group">
          <el-option  v-for="item in groups"
            :label="item"
            :key="item"
            :value="item">
          </el-option>
        </el-select>
        </el-form-item>
        <el-form-item label="工作模式：" >
          <el-select v-model="empInfo.workMode">
          <el-option  v-for="item in workMode"
            :label="item"
            :key="item"
            :value="item">
          </el-option>
        </el-select>
        </el-form-item>
        <el-form-item label="所属岗位：">
          <el-select v-model="empInfo.position">
          <el-option  v-for="item in position"
            :label="item"
            :key="item"
            :value="item">
          </el-option>
        </el-select>
        </el-form-item>
        <el-form-item label="奖金比例">
          <el-select v-model="empInfo.bounsRatio">
          <el-option  v-for="item in bounsRatio"
            :label="item"
            :key="item"
            :value="item">
          </el-option>
        </el-select>
        <el-button type="text" @click="dialogTableVisible = true">协议制奖金比例查看</el-button>
       
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm">修改提交</el-button>
          <el-button  @click="resetForm" >取消</el-button>
        </el-form-item>
      </el-form>
       <el-dialog title="协议制奖金比例" :visible.sync="dialogTableVisible" width="80%">
          <el-table :data="tableBounsRatios">
            <el-table-column property="yTime" label="入职时间"></el-table-column>
            <el-table-column property="rate2" label="大专及以下"></el-table-column>
            <el-table-column property="rate3" label="非双一流本科"></el-table-column>
            <el-table-column property="rate4" label="双一流本科"></el-table-column>
            <el-table-column property="rate5" label="非双一流研究生"></el-table-column>
            <el-table-column property="rate6" label="双一流研究生"></el-table-column>
          </el-table>
        </el-dialog>
    </div>
    </div>
  </div>
</template>



<script>
import Cookies from "js-cookie";
  export default {
    name: 'empDetails',
    data () {
    return {
      // username
      userName:'',
      // 弹窗
      dialogTableVisible:false,
      tableBounsRatios:[],
      empInfo:{},
      /////////////需修改后面从后台读取//////////////////////
      groups:['甲','乙','丙'],
      workMode:["两班倒","三班倒","四班三倒"],
      position:["包装挡车工","包装副挡车工","卷接挡车工","卷接副挡车工"],
      bounsRatio:["45%","50%","55%","60%","65%","100%"]
    }

    },
    methods:{
      getBounsRate:function()
      {
        this.$http.get("http://localhost:3000/BounsRatios")
          .then(function(response){
            
            this.tableBounsRatios = response.body;
            console.log( this.tableBounsRatios);
          })
      },
      getEmpInfo:function()
      {
         // this.$http.get("http://localhost:3000/empInfos/1")
         //  .then(function(response){
            
         //    this.empInfo = response.body;
         //    console.log( this.empInfo);
         //  })
        this.userName = sessionStorage.getItem('user');
        this.$api.empPostInfo.findEmpByName({user:this.userName}).then((res) => {
        // 处理异常
          if(res.msg)
          {
            this.$message({message: '操作失败, ' + res.msg, type: 'error'})
          }
         console.log(res.data)
          this.empInfo = res.data
      })
      }
    },
    created(){
      this.getEmpInfo();
      this.getBounsRate();
    }

  }
</script>

<style lang="scss">
    .el-row{
      margin:15px;
      padding:15px;
    }
</style>



