<template>
  <div class="container">  
    <div class="emp-detail" style="float:left;padding-top:5px;">
      <el-form ref="emp" :model="empInfo" :span="100" label-width="120px" style="margin:20px;width:60%;min-width:600px;">
        <el-form-item label="工  号：" >
           <el-input v-model="empInfo.eno"></el-input>
        </el-form-item>
        <el-form-item label="姓名：" >
          <el-input v-model="empInfo.name"></el-input>
        </el-form-item>
        <el-form-item label="岗位：" >
          <el-input v-model="empInfo.position"></el-input>
        </el-form-item>
        <el-form-item label="班组：" >
          <el-input v-model="empInfo.group"></el-input>
        </el-form-item>
         <el-form-item label="所在机台：" >
          <el-input v-model="empInfo.mid+'#'"></el-input>
        </el-form-item>
        <el-form-item label="奖金系数：" >
          <el-input v-model="empInfo.bounsRatio"></el-input>
        </el-form-item>
        <el-button style="margin-left:80px" type="text" @click="dialogTableVisible = true">协议制奖金比例查看</el-button>
        </el-form-item>
      </el-form>
       <el-dialog title="协议制奖金比例" :visible.sync="dialogTableVisible" width="80%">
          <el-table :data="tableBounsRatios">
            <el-table-column property="yTime" label="入职时间"></el-table-column>
            <el-table-column property="rate2" label="大专及以下"></el-table-column>
            <el-table-column property="rate3" label="非双一流本科"></el-table-column>
            <el-table-column property="rate4" label="双一流本科"></el-table-column>
            <el-table-column property="rate5" label="非双一流研究生"></el-table-column>
            <el-table-column property="rate6" label="双一流研究生"></el-table-column>
          </el-table>
        </el-dialog>
    </div>
    </div>
  </div>
</template>



<script>
import Cookies from "js-cookie";
  export default {
    name: 'empDetails',
    data () {
    return {
      // username
      userName:'',
      // 弹窗
      dialogTableVisible:false,
      empInfo:{},
    
      tableBounsRatios:[{
          "yTime":"2年",
          "rate2":"40%",
          "rate3":"50%",
          "rate4":"50%",
          "rate5":"65%",
          "rate6":"70%"},
          {
          "yTime":"3年",
          "rate2":"45%",
          "rate3":"55%",
          "rate4":"55%",
          "rate5":"80%",
          "rate6":"85%"},
          {
          "yTime":"4年",
          "rate2":"50%",
          "rate3":"60%",
          "rate4":"100%",
          "rate5":"100%",
          "rate6":"100%"},
          {
          "yTime":"5年",
          "rate2":"55%",
          "rate3":"65%",
          "rate4":"100%",
          "rate5":"100%",
          "rate6":"100%"}
        ],
    }

    },
    methods:{
      getEmpInfo:function()
      {
         // this.$http.get("http://localhost:3000/empInfos/1")
         //  .then(function(response){
            
         //    this.empInfo = response.body;
         //    console.log( this.empInfo);
         //  })
        this.userName = sessionStorage.getItem('user');
        this.$api.empPostInfo.findEmpByName({name:this.userName}).then((res) => {
        // 处理异常
          if(res.msg)
          {
            this.$message({message: '操作失败, ' + res.msg, type: 'error'})
          }
         console.log(res.data)
          this.empInfo = res.data
      })
      }
    },
    created(){
      this.getEmpInfo();
    }

  }
</script>

<style lang="scss">
    .el-row{
      margin:15px;
      padding:15px;
    }
</style>



