<template>
  <div class="pagecontainer">  
    <div class="add-detyExecpt" style="margin-top:50px;">
      <span>新增考勤异常数据</span>
      <el-form ref="empForm" :inline="true" :model="empDutyFrom"  label-width="120px" style="margin:20px;width:60%;min-width:600px;" :loading="editLoading"> 
        <el-form-item label="姓名：" prop="name">
         <el-autocomplete
         v-model="empSelect"
         :fetch-suggestions="querySearchAsync"
         @select="handleSelect"
         :debounce=10
          placeholder="请输入姓名自动匹配查找">
        </el-autocomplete>
        </el-form-item>
        <el-form-item label="班组：" prop="group">
          <el-select v-model="empDutyFrom.group">
          <el-option  v-for="item in groups"
            :label="item.label"
            :key="item.id"
            :value="item.id">
          </el-option>
        </el-select>
        </el-form-item>
       
        <el-form-item label="起始时间" prop="stime">
         <el-date-picker type="datetime"  v-model="empDutyFrom.stime" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
        </el-form-item>
         <el-form-item label="终止时间" prop="etime">
          <el-date-picker type="datetime"  v-model="empDutyFrom.etime" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
        </el-form-item>

          <el-form-item label="请假类型：" prop="absId">
          <el-select v-model="empDutyFrom.absId">
          <el-option  v-for="item in absTypes"
            :label="item.label"
            :key="item.id"
            :value="item.id">
          </el-option>
        </el-select>
        </el-form-item>
        <el-form-item style="margin-left:60px">
          <el-button type="primary" @click="submitForm" size="small">提交</el-button>
          <el-button  @click="reset" size="small">取消</el-button>
        </el-form-item>
      </el-form>

    </div>

    <div class="orderHandle">
      <span>考勤异常审批</span>
      <!-- 数据table 通过 驳回 批量审批 -->
      <el-table ref="table" :data="dutyOrders.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width:80%"  @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55">
      </el-table-column>
       <el-table-column prop="id" label="序号"  sortable width="90%">
        </el-table-column>
        <el-table-column prop="name" label="姓名" width="90%">
        </el-table-column>
        <el-table-column prop="group" label="班组" width="90%">
        </el-table-column>
        <el-table-column prop="absDesc" label="请假原因" width="120%">
        </el-table-column>
        <el-table-column prop="stime" label="起始时间" width="180%">
        </el-table-column>
        <el-table-column prop="etime" label="终止时间"width="180%">
        </el-table-column>
        </el-table-column>
        <el-table-column label="操作">
          <template scope="scope">
          <el-button type="primary" @click="orderConfirm(scope.$index,scope.row)" size="mini">通过</el-button>
           <el-button type="danger" @click="orderDeny(scope.$index,scope.row)" size="mini">驳回</el-button>
         </template>
        </el-table-column>
      </el-table>
       <el-col :span="6" class="toolbar">
        <el-button type="primary" @click="batchOrderConfirm" size="mini">批量确认</el-button>
      </el-col>
      <el-col :span="18" class="toolbar">
       <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 20, 40]" 
            :page-size="pagesize"         
            layout="total, sizes, prev, pager, next, jumper"
            :total="this.dutyOrders.length">
      </el-pagination>
    </el-col>

    </div>
  </div>
</template>



<script>
  import Cookies from "js-cookie"
  export default {
    name: 'DutyExpMgt',
    data () {
    return {
    dutyOrders:[],
    // 通用filter查询
    filter:{
      name:'', // 用户名 
      brand:'',
      machineno:'',
     sTime:"",
     eTime:"",
     createTime:""
    },
    //分页
    currentPage:1, //初始页
    pagesize:5,    //每页的数据
    // 班组
    groups:[
      {"id":"1","label":"甲班"},
      {"id":"2","label":"乙班"},
      {"id":"3","label":"丙班"},
      {"id":"4","label":"丁班"},
      {"id":"5","label":"细甲"},
      {"id":"6","label":"细乙"},
      {"id":"7","label":"细丙"},
      {"id":"8","label":"细丁"}],
      // 工作模式
      workModes:[
      {"id":"1","mode":"两班倒"},
      {"id":"2","mode":"三班倒"},
      {"id":"3","mode":"三班两运转"},
      {"id":"4","mode":"四班三运转"}],

      // 表单数据
      empDutyFrom:{
        "name":'',
        "group":'',
        "absId":'',
        "stime":'',
        "etime":''
      },
      // 选中的姓名
       empSelect: "",
       options:[],

      // 全部员工岗位信息 emp_post_info
       empPostInfos:[], 
        // 请假类型
      absTypes:[
        {"id":"1","label":"休息"},
        {"id":"2","label":"出差"},
        {"id":"3","label":"病假"},
        {"id":"4","label":"事假"},
        {"id":"5","label":"产假"},
        {"id":"6","label":"丧假"},
        {"id":"7","label":"探亲假"},
        {"id":"8","label":"陪护假"},
        {"id":"9","label":"年假"},
        {"id":"10","label":"补休（有补休单）"},
        {"id":"11","label":"补休（无补休单）"}],    
      machinenos : [
        {"id":"1","label":"1#"},
        {"id":"2","label":"2#"},
        {"id":"3","label":"3#"},
        {"id":"4","label":"4#"},
        {"id":"5","label":"5#"},
        {"id":"6","label":"6#"},
        {"id":"7","label":"7#"},
        {"id":"8","label":"8#"},
        {"id":"9","label":"9#"},
        {"id":"10","label":"10#"},
        {"id":"11","label":"11#"},
        {"id":"12","label":"12#"},
        {"id":"13","label":"13#"},
        {"id":"14","label":"14#"},
        {"id":"15","label":"15#"},
        {"id":"16","label":"16#"},
        {"id":"17","label":"17#"},
        {"id":"18","label":"18#"},
        {"id":"19","label":"19#"},
        {"id":"20","label":"20#"},
        {"id":"21","label":"21#"},
        {"id":"22","label":"22#"},
        {"id":"23","label":"23#"},
        {"id":"24","label":"24#"},
        {"id":"25","label":"25#"},
        {"id":"26","label":"26#"},
        {"id":"27","label":"27#"},
        {"id":"28","label":"28#"},
        {"id":"29","label":"29#"},
        {"id":"30","label":"30#"}],
      // 选中机台号列表
       selectedArray: [],
      // 弹窗
      dialogTableVisible:false,
      tableBounsRatios:[],
      editLoading:false,
      userName:''
    }
    },
    methods:{
      initUserName:function()
      {
        this.userName = sessionStorage.getItem('user');
      },

       // 单条审批通过
      orderConfirm:function(index,row)
      {
          var rows = new Array();
          rows.push(row);
          console.log(rows) // 后台接收的是数组
        this.$api.dutyExcept.batchOrderConfirm(rows)
            .then((res) => {
         console.log(res.data)
        if(res.code == 200) {
        this.$message({message: '操作成功', type: 'success'})
        this.findDutyOrders();// 刷新数据
      } else {
        this.$message({message: '操作失败, ' + res.msg, type: 'error'})
       }
     }).catch(() => {
      })

      },

      // 审批驳回 审批状态置-1
      orderDeny:function(index,row)
      {
        this.$api.dutyExcept.orderDeny(row)
            .then((res) => {
         console.log(res.data)
        if(res.code == 200) {
        this.$message({message: '操作成功', type: 'success'})
         this.findDutyOrders();// 刷新数据
      } else {
        this.$message({message: '操作失败, ' + res.msg, type: 'error'})
       }
     }).catch(() => {
      })

      },
      // 批量审批
      batchOrderConfirm:function()
      { 
        console.log(this.selections)
       this.$api.dutyExcept.batchOrderConfirm(this.selections)
            .then((res) => {
         console.log(res.data)
        if(res.code == 200) {
        this.$message({message: '操作成功', type: 'success'})
         this.findDutyOrders();// 刷新数据
      } else {
        this.$message({message: '操作失败, ' + res.msg, type: 'error'})
       }
     }).catch(() => {
      })
      },

      // 初始化查询条件
      initFilter:function()
      {
        // name
         this.filter.name = this.userName;
        
      },
      // 查询考勤数据修改申请单
      findDutyOrders:function()
      {
        this.initFilter();
        this.$api.dutyExcept.findDutyOrders(this.filter).then((res)=>
        {
          console.log(res.data)
          this.dutyOrders = res.data;
        })
      },

    // 批量选择修改
     handleSelectionChange: function (selections) {
      this.selections = selections
      console.log(this.selections)
      this.$emit('selectionChange', {selections:selections})
    },
    // 初始页currentPage、初始每页数据数pagesize和数据data
    handleSizeChange:function(size) {
      this.pagesize = size;
      console.log(this.pagesize)  //每页下拉显示数据
    },
    handleCurrentChange:function(currentPage){
      this.currentPage = currentPage;
      console.log(this.currentPage)  //点击第几页
    },
    
      reset() {
        this.$refs.empForm.resetFields()
      },
      ////////下拉选择后台获取/////////////
      ///岗位
      getPositions:function()
      {
        // this.$http.get("http://localhost:3000/empDutyFrom")
        //     .then(function(response){
        //        console.log(response.body);
        //       this.empDutyFroms = response.body;
        //        this.options = this.empDutyFroms;

        //     })
      },
      ///机台号
      ///工作模式
      ///
     changeSelect(val) {
      console.log(val.length)
        if (val.length === this.machinenos.length) {
        this.checked = true
        } else {
        this.checked = false
        }
      },
       selectAll() {
        this.selectedArray = []
        if (this.checked) {
        this.machinenos.map((item) => {
         this.selectedArray.push(item)
        })
        console.log(this.selectedArray)
        } else {
        this.selectedArray = []
        }
       },
     
     //获取下拉员工信息/////////// 暂时测试使用///////////
      getInitData:function()
      {
         // this.$http.get("http://localhost:3000/empPostInfo")
         //    .then(function(response){
         //       console.log(response.body);
         //      this.empPostInfos = response.body;
         //       this.options = this.empPostInfos;
         //    })
            
          this.$api.empPostInfo.findEmpInfos().then((res) => {
             console.log(res.data);
            this.empPostInfos = res.data;
             this.options = this.empPostInfos;})
      },
   //queryString 为在框中输入的值    callback 回调函数,将处理好的数据推回
    querySearchAsync(queryString, callback) {
         let list = [{}];
         let param = {
              pageNum: 1,
              pageSize: 9999,
              key: queryString
         }      
         
//      let data = this.getAccountUserInfo(param,0);
        this.options.forEach(function(item,i){
              if(item.name.indexOf(queryString) != -1){
                    list.push({"value":item.name});   
              }      
        })
        console.log(queryString)
        if(queryString){
           callback(list);
         }
    },
    // 模糊查询下拉框 鼠标键盘选中点击触发
    handleSelect(item) { 
         this.empSelect = item.value;
         this.initFormData();
    },
    // 根据选中员工姓名，初始化表单数据
    initFormData:function()
    {
      var empInfo = this.matchSelect();
      console.log(empInfo)
      //////////////根据后台返回构造数据 这里需要修改 
      this.empDutyFrom.name = empInfo.name;
      this.empDutyFrom.group = empInfo.group;
      
    },
    // 根据姓名匹配
    matchSelect:function()
    {
      for (var i = this.empPostInfos.length - 1; i >= 0; i--) {
          if( this.empSelect == this.empPostInfos[i].name)
          {
            return this.empPostInfos[i];
          }
      }
        
    },
    // 设置新增表单数据
    setFromData:function()
    {
      // 添加创建人
      this.empDutyFrom.createBy = this.userName;
    },
    // 表单提交
    submitForm: function () {
      
        this.$confirm('确认提交吗？', '提示', {}).then(() => {
          this.editLoading = true
          this.setFromData();
          console.log(this.empDutyFrom)
          let params = Object.assign({}, this.empDutyFrom)
          this.$api.dutyExcept.addDutyExcept(params).then((res) => {
            if(res.code == 200) {
              this.$message({ message: '操作成功', type: 'success' })
             this.$refs.empForm.resetFields()
            } else {
              this.$message({message: '操作失败, ' + res.msg, type: 'error'})
            }
          
          })
        })
    },

    },
    created(){

      // 获取登陆用户
      this.initUserName()

       this.getInitData()
       // 岗位下拉初始化
       this.getPositions()
       // 获取初始考勤异常申请单
       this.findDutyOrders()
    
    }

  }
</script>

<style lang="scss">
    .el-row{
      margin:15px;
      padding:15px;
    }
</style>





