<template>
  <div id="app">
    <div class="container">
      <form>
        <div class="col-sm-push-2 col-sm-8">
          <div class="asbdy" v-for="(item ,index) in pageData">
            <!-题干->
            <div class="question">
              <span class="num">{{(currentPage-1)*pageSize+index+1}}、</span>
              {{item.tiMu}}
              {{item.type==0?' (单选题)':' (多选题)'}}
            </div>
            <!-选项->
            <div class="anser">
              <ul>
                <li class="xuanXiang" v-for="(tiao,i) in item.xuanXiang" :class="{'double-line':false}">
                  <div v-if="item.type==0">
                    <input
                    type="radio"
                    v-model="item.picked_radio"
                    :name="'pickedd_' +((currentPage-1)*pageSize+index)"
                    v-bind:value="haha[i]"
                    @change="doCheck(item,((currentPage-1)*pageSize+index))"
                    />
                    {{haha[i]}} {{tiao}}
                  </div >
                  
                  <div  v-if="item.type==1">
                    <input
                    type="checkbox"
                    v-model="item.pickedMany"
                    :name="'pickedd_' +((currentPage-1)*pageSize+index)"
                    v-bind:value="haha[i]"
                    @change="doCheck(item,((currentPage-1)*pageSize+index))"
                    />
                    {{haha[i]}} {{tiao}}
                  </div>
                  
                  
                </li>
              </ul>
            </div>
            
            
            <div class="clearfix"></div>
          </div>
          
          <!-- 模态框（Modal） -->
          <div class="modal fade" id="myModal" tabindex="-1" role="dialog"
          aria-labelledby="myModalLabel" aria-hidden="true">
          <div class="modal-dialog">
            <div class="modal-content">
              <div class="modal-header">
                <button type="button" class="close" data-dismiss="modal"
                aria-hidden="true">&times;
              </button>
              <h4 class="modal-title" id="myModalLabel">答题情况</h4>
            </div>
            <!-- 内容 -->
            <div class="modal-body">
              <ul class="modal-u">
                <li v-for="(item,index) in chengji">
                  <span :class="{unselect:!item.isChose,is_right:item.isRight,is_error
                    :(!item.isRight&&item.isRight!=undefined)}">
                    第{{index+1}}题
                  </span>
                </li>
                
              </ul>
            </div>
            <div class="clearfix"></div>
            <div class="modal-footer">
              <button type="button" class="btn btn-default" data-dismiss="modal">关闭</button>
              <!-- <button type="button" class="btn btn-primary saveBtn">确定</button> -->
            </div>
          </div>
          <!-- /.modal-content -->
        </div>
      </div>
    </div>
    <div class="col-sm-offset-2 col-sm-8 dtbtn">
      <button type="button" class="btn btn-primary" style="width: 80px" @click="prev">上一题</button>
      <button type="button" class="btn btn-primary" style="width: 80px" @click="next">下一题</button>
      <button type="button" class="btn btn-primary" style="width: 80px" @click="detail"  data-toggle="modal"
      data-target="#myModal">答题状况</button>
    </div>
  </form>
</div>
</div>
</template>

<script>
  import 'bootstrap/dist/css/bootstrap.min.css'
  import 'bootstrap/dist/js/bootstrap.min.js'
  import Cookies from "js-cookie";
  export default {

    data() {
      return {
       name: "zhangsan",
            currentPage: 1, //当前第几页
            pageSize: 2,  //每页显示几条数据
            haha: ["A", "B", "C", "D"],
            pageData: [],  //单页数据
            chengji: [
            {
              isRight: null,
              picked_radio: "",
              pickedMany:[],
              daan: ["C"],
              tiMu: "1*8=?",
              xuanXiang: ["2", "6", "8", "10"],
              type: 0
            },
            {
              isRight: null,
              picked_radio: "",
              pickedMany:[],
              daan: ["D"],
              tiMu: "2*5=?",
              xuanXiang: ["2", "6", "8", "10"],
              type: 0
            },
            {
              isRight: null,
              picked_radio: "",
              pickedMany:[],
              daan: ["A", "B", "D"],
              tiMu: "下面哪些城市属于河南？",
              xuanXiang: ["郑州", "开封", "阜阳", "信阳"],
              type: 1
            },
            {
              isRight: null,
              picked_radio: "",
              pickedMany:[],
              daan: ["D"],
              tiMu: "纽约是下面哪个国家的城市?",
              xuanXiang: ["中国", "日本", "韩国", "美国"],
              type: 0
            },
            {
              isRight: null,
              ppicked_radio: "",
              pickedMany:[],
              daan: ["A"],
              tiMu: "2*8=?",
              xuanXiang: ["16", "6", "8", "10"],
              type: 0
            },
            {
              isRight: null,
              picked_radio: "",
              pickedMany:[],
              daan: ["B", "D"],
              tiMu: "下面哪些属于植物?",
              xuanXiang: ["狗", "棉花", "猫", "水稻"],
              type: 1
            }]
          }
        },
        methods: {
          init() {
            var $this = this;
            
            this.pageData = this.chengji.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize);
            this.pageData.forEach(function (item, i) {
              if(item.type==0){
                item.picked_radio = Cookies.get('pickedd_' + (($this.currentPage - 1) * $this.pageSize + i));
              }else{
                if(JSON.parse(Cookies.get('pickedd_' + (($this.currentPage - 1) * $this.pageSize + i)))==null||
                  JSON.parse(Cookies.get('pickedd_' + (($this.currentPage - 1) * $this.pageSize + i)))==undefined){
                  item.pickedMany=[];
              }else{
                item.pickedMany = JSON.parse(Cookies.get('pickedd_' + (($this.currentPage - 1) * $this.pageSize + i)));
              }
            }
            item.isRight = Cookies.get('isRightt_' + (($this.currentPage - 1) * $this.pageSize + i));
          });
          },
          doCheck(item, index) {
            if(item.type==0){
                    //单选
                    if (item.picked_radio == item.daan[0]) {
                      item.isRight = true;
                    } else {
                      item.isRight = false;
                    }
                  }else{
                    //多选
                    if (JSON.stringify(item.pickedMany.sort()) == JSON.stringify(item.daan.sort())) {
                      item.isRight = true;
                    } else {
                      item.isRight = false;
                    }
                  }
                  
                // 将结果放入缓存
                if(item.type==0){
                  Cookies.set('pickedd_' + index, item.picked_radio);
                  
                }else{
                  Cookies.set('pickedd_' + index, JSON.stringify(item.pickedMany));
                }
                
                Cookies.set('isRightt_' + index, item.isRight);
                Cookies.set('isChose_' + index, true);
                
              },
            prev() {  //上一页
              if (this.currentPage > 1) {
                this.currentPage = this.currentPage - 1;
                this.init();
              }
            },
            next() {  //下一页
              if (this.currentPage < this.allPageNum) {
                this.currentPage = this.currentPage + 1;
                this.init();
              }
            },
            detail() { //答题状况
              var $this = this;
              this.chengji.forEach(function (item, i) {
                item.isRight = JSON.parse(Cookies.get('isRightt_' + i));
                item.isChose = JSON.parse(Cookies.get('isChose_' + i));
                    //下面注释的是错误写法
                    //item.isRight = Cookies.get('isRightt_' + i);
                    //item.isChose = Cookies.get('isChose_' + i);
                  });
            }
          },
          computed: {
            allPageNum: {
              get: function () {
                return parseInt((this.chengji.length + this.pageSize - 1) / this.pageSize);
              }
            }
          },
          mounted() {
            this.init();
          }
        }
      </script>

      <style lang="scss">
        .site-wrapper.site-page--not-found {
          position: absolute;
          top: 60px;
          right: 0;
          bottom: 0;
          left: 0;
          overflow: hidden;
          .site-content__wrapper {
            padding: 0;
            margin: 0;
            background-color: #fff;
          }
          .site-content {
            position: fixed;
            top: 15%;
            left: 50%;
            z-index: 2;
            padding: 30px;
            text-align: center;
            transform: translate(-50%, 0);
          }
          .not-found-title {
            margin: 20px 0 15px;
            font-size: 8em;
            font-weight: 500;
            color: rgb(55, 71, 79);
          }
          .not-found-desc {
            margin: 0 0 30px;
            font-size: 26px;
            text-transform: uppercase;
            color: rgb(118, 131, 143);
            > em {
              font-style: normal;
              color: #ee8145;
            }
          }
          .not-found-btn-gohome {
            margin-left: 30px;
          }
        }
      </style>



