<template>
  <div class="pagecontainer">  
    <div class="add-postchange" style="margin-top:50px;">
     <span>新增日岗位变动</span>
      <el-form ref="empForm" :inline="true" :model="postChangeForm"  :rules="postChangeFormRules" label-width="120px" style="margin:20px;width:60%;min-width:600px;" :loading="editLoading"> 

        <el-form-item label="姓名：" prop="username">
         <el-autocomplete
           v-model="empSelect"
           :fetch-suggestions="querySearchAsync"
           @select="handleSelect"
           :debounce=10
            placeholder="请输入姓名"
       >
      </el-autocomplete>
        </el-form-item>
        <el-form-item label="原班组：" prop="group">
          <el-select v-model="postChangeForm.group">
          <el-option  v-for="item in groups"
            :label="item.label"
            :key="item.id"
            :value="item.id">
          </el-option>
        </el-select>
        </el-form-item>
        <el-form-item label="工作模式：" prop="workMode">
          <el-select v-model="postChangeForm.wid">
          <el-option  v-for="item in workModes"
            :label="item.label"
            :key="item.id"
            :value="item.id">
          </el-option>
        </el-select>
        </el-form-item>
        <el-form-item label="原有岗位：" prop="position">
          <el-select v-model="postChangeForm.bpid">
          <el-option  v-for="item in positions"
            :label="item.position"
            :key="item.id"
            :value="item.id">
          </el-option>
        </el-select>
        </el-form-item>
        <el-form-item label="起始时间" prop="stime">
         <el-date-picker type="datetime"  v-model="postChangeForm.stime" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
        </el-form-item>
         <el-form-item label="终止时间" prop="etime">
          <el-date-picker type="datetime"  v-model="postChangeForm.etime" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
        </el-form-item>
         <el-form-item label="当前所在机台：" prop="machine">
          <el-input v-model="postChangeForm.machineno"></el-input>
          <!-- <span>{{postChangeForm.machineno}}</span> -->
        </el-form-item>
         <el-form-item label="修改机台号:" prop="machines">
         <el-select multiple collapse-tags v-model='selectedArray' @change='changeSelect' placeholder='请选择'>
           <el-checkbox v-model="checked" @change='selectAll'>全选</el-checkbox>
           <el-option v-for='(item, index) in machinenos' :key='id' :label='item.label' :value='item.id'></el-option>
          </el-select>
        </el-form-item>
        <el-form-item style="margin-left:300px">
          <el-button type="primary" @click="submitForm" size="small">修改提交</el-button>
          <el-button  @click="reset" size="small">取消</el-button>
        </el-form-item>
      </el-form>

    </div>
    <div class="orderHandle">
      <span>顶岗异常审批</span>
      <!-- 数据table 通过 驳回 批量审批 -->
      <el-table ref="table" :data="postOrders.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width:80%"  @selection-change="handleSelectionChange">
       
      <el-table-column type="selection" width="55">
      </el-table-column>
       <el-table-column type="index" width="50" label="序号">
        </el-table-column>
        <el-table-column prop="name" label="姓名" width="90%">
        </el-table-column>
        <el-table-column prop="group" label="班组" width="80%">
        </el-table-column>
        <el-table-column prop="machineno" label="机台号" width="80%">
        </el-table-column>
        <el-table-column prop="position" label="岗位" width="150%">
        </el-table-column>
        <el-table-column prop="stime" label="起始时间" width="150%">
        </el-table-column>
        <el-table-column prop="etime" label="终止时间"width="150%">
        </el-table-column>
        </el-table-column>
        <el-table-column label="操作">
          <template scope="scope">
          <el-button type="primary" @click="orderConfirm(scope.$index,scope.row)" size="mini">通过</el-button>
           <el-button type="danger" @click="orderDeny(scope.$index,scope.row)" size="mini">驳回</el-button>
         </template>
        </el-table-column>
      </el-table>
       <el-col :span="6" class="toolbar">
        <el-button type="primary" @click="batchOrderConfirm" size="mini">批量确认</el-button>
      </el-col>
      <el-col :span="18" class="toolbar">
       <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 20, 40]" 
            :page-size="pagesize"         
            layout="total, sizes, prev, pager, next, jumper"
            :total="this.postOrders.length">
      </el-pagination>
    </el-col>

    </div>
  </div>
</template>



<script>
  import Cookies from "js-cookie"
  export default {
    name: 'PostChangeMgt',
    data () {
    return {
       // 通用filter查询
      filter:{
        name:'', // 用户名 
        brand:'',
        machineno:'',
       sTime:"",
       eTime:"",
       createTime:""
      },
      // 审批请求数据
      // postOrders:[],
       postOrders:[
{"name":"用户14","group":"甲班","machineno":"3","position":"包装副挡车工","createTime":"2019/8/16"},
{"name":"用户15","group":"甲班","machineno":"4","position":"包装副挡车工","createTime":"2019/8/17"},
{"name":"用户16","group":"甲班","machineno":"5","position":"包装副挡车工","createTime":"2019/8/18"},
{"name":"用户17","group":"甲班","machineno":"6","position":"包装副挡车工","createTime":"2019/8/19"},
{"name":"用户18","group":"甲班","machineno":"7","position":"包装副挡车工","createTime":"2019/8/20"}
],
       //分页
      currentPage:1, //初始页
      pagesize:5,    //每页的数据

    // 班组
    groups:[
      {"id":"1","label":"甲班"},
      {"id":"2","label":"乙班"},
      {"id":"3","label":"丙班"},
      {"id":"4","label":"丁班"},
      {"id":"5","label":"细甲"},
      {"id":"6","label":"细乙"},
      {"id":"7","label":"细丙"},
      {"id":"8","label":"细丁"}],
      // 工作模式
      workModes:[
      {"id":"1","label":"两班倒"},
      {"id":"2","label":"三班倒"},
      {"id":"3","label":"三班两运转"},
      {"id":"4","label":"四班三运转"}],

      // 岗位
      positions:[],

      // 表单数据
      postChangeForm:{
        "name":'',
        "group":'',
        "wid":'',
        "bpid":'',
        "machineno":'',
        "createBy":'',
        "stime":'',
        "etime":''
      },
      // 表单规则校验
      postChangeFormRules:{
        stime:[
          { required: true, message: '起始时间不能为空', trigger: 'blur'}
        ],
        etime:[
          { required: true, message: '终止时间不能为空', trigger: 'blur'}
        ],
      },
      test:'asdsdfsd测试',
      // 选中的姓名
       empSelect: "",
       options:[],
       // 全部员工岗位信息 emp_post_info
       empPostInfos:[],       
      machinenos : [
      {"id":"1","label":"1#"},
      {"id":"2","label":"2#"},
      {"id":"3","label":"3#"},
      {"id":"4","label":"4#"},
      {"id":"5","label":"5#"},
      {"id":"6","label":"6#"},
      {"id":"7","label":"7#"},
      {"id":"8","label":"8#"},
      {"id":"9","label":"9#"},
      {"id":"10","label":"10#"},
      {"id":"11","label":"11#"},
      {"id":"12","label":"12#"},
      {"id":"13","label":"13#"},
      {"id":"14","label":"14#"},
      {"id":"15","label":"15#"},
      {"id":"16","label":"16#"},
      {"id":"17","label":"17#"},
      {"id":"18","label":"18#"},
      {"id":"19","label":"19#"},
      {"id":"20","label":"20#"},
      {"id":"21","label":"21#"},
      {"id":"22","label":"22#"},
      {"id":"23","label":"23#"},
      {"id":"24","label":"24#"},
      {"id":"25","label":"25#"},
      {"id":"26","label":"26#"},
      {"id":"27","label":"27#"},
      {"id":"28","label":"28#"},
      {"id":"29","label":"29#"},
      {"id":"30","label":"30#"}
      ],
      // 选中机台号列表
       selectedArray: [],
      // 选中的员工
      matchEmpInfo:{},
      // 弹窗
      dialogTableVisible:false,
      tableBounsRatios:[],
      empPostInfo:[],
      editLoading:false,
      selections: [],  // 列表选中列
      userName:''
    }
    },
    methods:{

        initUserName:function()
      {
        this.userName = sessionStorage.getItem('user');
        console.log(this.userName);
      },

      // 单条审批通过
      orderConfirm:function(index,row)
      {
         var rows = new Array();
        rows.push(row);
        console.log(rows) // 后台接收的是数组
        this.$api.position.batchOrderConfirm(rows)
            .then((res) => {
         console.log(res.data)
        if(res.code == 200) {
        this.$message({message: '提交成功', type: 'success'})
        this.findPostOrders()
      } else {
        this.$message({message: '操作失败, ' + res.msg, type: 'error'})
       }
     }).catch(() => {
      })

      },

      // 审批驳回 审批状态置-1
      orderDeny:function(index,row)
      {
        this.$api.position.orderDeny(row)
            .then((res) => {
         console.log(res.data)
        if(res.code == 200) {
          this.findPostOrders();
        this.$message({message: '提交成功', type: 'success'})
      } else {
        this.$message({message: '操作失败, ' + res.msg, type: 'error'})
       }
     }).catch(() => {
      })

      },
      // 批量审批
      batchOrderConfirm:function()
      { 
        console.log(this.selections)
       this.$api.position.batchOrderConfirm(this.selections)
            .then((res) => {
         console.log(res.data)
        if(res.code == 200) {
        this.$message({message: '提交成功', type: 'success'})
        this.findPostOrders()
      } else {
        this.$message({message: '操作失败, ' + res.msg, type: 'error'})
       }
     }).catch(() => {
      })
      },



      // 初始化查询条件
      initFilter:function()
      {
        // name
         this.filter.name = this.userName;
      },
      // 查询顶岗数据修改申请单
      findPostOrders:function()
      {
        this.initFilter();
        this.$api.position.findPostOrders(this.filter).then((res)=>
        {
          console.log(res.data)
          this.postOrders = res.data;
        })
      },


      // 批量选择修改
     handleSelectionChange: function (selections) {
      this.selections = selections
      console.log(this.selections)
      this.$emit('selectionChange', {selections:selections})
    },
    // 初始页currentPage、初始每页数据数pagesize和数据data
    handleSizeChange:function(size) {
      this.pagesize = size;
      console.log(this.pagesize)  //每页下拉显示数据
    },
    handleCurrentChange:function(currentPage){
      this.currentPage = currentPage;
      console.log(this.currentPage)  //点击第几页
    },
      reset() {
        this.$refs.empForm.resetFields()
        this.postChangeForm.name = "";
        this.postChangeForm.group = "";
        this.postChangeForm.machineno ="";
        this.postChangeForm.wid = "";
        this.postChangeForm.bpid = "";
        this.selectedArray =[];
      },
      ////////下拉选择后台获取/////////////
      ///岗位
      getPositions:function()
      {
         this.$api.basicPosition.findAllOperator().then((res) => {
         console.log(res.data);
          this.positions = res.data;
        })
      },
      
     changeSelect(val) {
      console.log(val.length)
        if (val.length === this.machinenos.length) {
        this.checked = true
        } else {
        this.checked = false
        }
      },
       selectAll() {
        this.selectedArray = []
        if (this.checked) {
        this.machinenos.map((item) => {
         this.selectedArray.push(item)
        })
        console.log(this.selectedArray)
        } else {
        this.selectedArray = []
        }
       },
     
   //queryString 为在框中输入的值    callback 回调函数,将处理好的数据推回
    querySearchAsync(queryString, callback) {
         let list = [{}];
         let param = {
              pageNum: 1,
              pageSize: 9999,
              key: queryString
         }      
         
//      let data = this.getAccountUserInfo(param,0);
        this.options.forEach(function(item,i){
              if(item.name.indexOf(queryString) != -1){
                    list.push({"value":item.name});   
              }      
        })
        // if(!queryString){
        //       list = list.splice(0,7);
        // }
        if(queryString){
          callback(list);
        }
    },
    // 模糊查询下拉框 鼠标键盘选中点击触发
    handleSelect(item) { 
         this.empSelect = item.value;
         this.initFormData();
    },
     getInitData:function()
      {
         // this.$http.get("http://localhost:3000/empPostInfo")
         //    .then(function(response){
         //       console.log(response.body);
         //      this.empPostInfos = response.body;
         //       this.options = this.empPostInfos;
         //    })

        // 下拉项 查询当月排班表所有的操作工信息
         this.$api.empPostInfo.findForPull().then((res) => {
         console.log(res.data);
        this.empPostInfos = res.data;
         this.options = this.empPostInfos;})
      },
    // 根据选中员工姓名，初始化表单数据
    initFormData:function()
    {
      this.matchEmpInfo = this.matchSelect();
      console.log(this.matchEmpInfo)
      //////////////根据后台返回构造数据 这里需要修改 
      this.postChangeForm.group = this.matchEmpInfo.group;
      this.postChangeForm.wid = this.matchEmpInfo.mode;
      this.postChangeForm.bpid = this.matchEmpInfo.position;
      this.postChangeForm.machineno = this.matchEmpInfo.mid;
      console.log(this.postChangeForm)
    },
    // 根据姓名匹配
    matchSelect:function()
    {
      for (var i = this.empPostInfos.length - 1; i >= 0; i--) {
          if( this.empSelect == this.empPostInfos[i].name)
          {
            return this.empPostInfos[i];
          }
      }
        
    },
    // 设置姓名，及创建人姓名，和修改后机台号
    setFormData:function()
    {
       this.postChangeForm.name = this.empSelect;
       this.postChangeForm.createBy = this.userName
       var temp = this.selectedArray;
       console.log(temp)
       // 修改了机台号
       if(this.selectedArray)
       {
          this.postChangeForm.machineno = temp.join(",");
       }
       // wid: "两班制" 转换 wid:"1"
       if(this.postChangeForm.wid == this.matchEmpInfo.mode)
       {
          this.postChangeForm.wid = this.matchEmpInfo.wid
       }
       // 设置岗位 bpid:"卷接挡车工"  ->  bpid:'15'
       if(this.postChangeForm.bpid == this.matchEmpInfo.position)
       {
          this.postChangeForm.bpid = this.matchEmpInfo.bpid
       }
       console.log(this.postChangeForm)

    },
    // 表单提交
    submitForm: function () {
      // 时间输入校验
       if(this.postChangeForm.stime >= this.postChangeForm.etime)
        {
           this.$message({ message: '起始时间不能大于终止时间', type: 'error' })
           return;
        }

        this.$confirm('确认提交吗？', '提示', {}).then(() => {
          this.editLoading = true
         
          // 设置姓名，和修改后机台号,wid,pid
          this.setFormData();
          let params = Object.assign({}, this.postChangeForm)
          console.log(params)
          this.$api.position.addEmpPost(params).then((res) => {
            if(res.code == 200) {
              this.$message({ message: '操作成功', type: 'success' })
            this.reset();// 清空表单
            } else {
              this.$message({message: '操作失败, ' + res.msg, type: 'error'})
            }
          })
        })
       }
    },
    created(){
      this.initUserName()
       this.getInitData()
       // 岗位下拉初始化
       this.getPositions()

       // table 初始化 查询所有未审核的顶岗异常
       this.findPostOrders()
    }

  }
</script>

<style lang="scss">
    .el-row{
      margin:15px;
      padding:15px;
    }
</style>





