<template>
   <div class="toolbar" style="margin:10px;">
     <!--工具条-->
     <div class="jbDutyExcepts">
      <el-col :span="18" class="toolbar">
        <h3>当月异常考勤数据</h3>
      </el-col>
     

      <!-- 查询数据产量定额显示 序号，机台，牌号，定额（这里表示的是常规生产8小时/班的定额）-->
      <el-table ref="table" :data="dutyExcepts.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width:80%"  @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55">
      </el-table-column>
       <el-table-column prop="id" label="序号"  sortable width="90%">
        </el-table-column>
        <el-table-column prop="name" label="姓名" width="90%">
        </el-table-column>
        <el-table-column prop="group" label="班组" width="90%">
        </el-table-column>
        <el-table-column prop="absDesc" label="请假类型" width="90%">
        </el-table-column>
        <el-table-column prop="stime" label="起始时间" width="200%">
        </el-table-column>
        <el-table-column prop="etime" label="终止时间" width="200%">
        </el-table-column>
        </el-table-column>
        <el-table-column label="操作">
          <template scope="scope">
          <el-button type="danger" @click="editDutyItem(scope.$index,scope.row)" size="mini">异常修改</el-button>
          </template>
        </el-table-column>
      </el-table>
       <el-col :span="6" class="toolbar">
        <el-button type="primary" @click="batchDutyCheck" size="mini">批量确认</el-button>
      </el-col>
      <el-col :span="18" class="toolbar">
       <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 20, 40]" 
            :page-size="pagesize"         
            layout="total, sizes, prev, pager, next, jumper"
            :total="this.dutyExcepts.length">
      </el-pagination>
    </el-col>

      </div>
      
       <!-- dutyForm 修改界面 -->
    <el-dialog title="修改" :visible.sync="dutyFormVisible">
      <el-form :model="dutyForm" label-width="100px" :rules="dutyFormRules" ref="dutyForm">
        <el-form-item label="请假类型">
           <el-select v-model="dutyForm.absId" placeholder="请选择">
              <el-option
                v-for="item in absTypes"
                :key="item.value"
                :label="item.label"
                :value="item.id">
              </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="请假开始时间">
          <el-date-picker type="date"  v-model="dutyForm.stime"></el-date-picker>
        </el-form-item>
        <el-form-item label="请假结束时间">
           <el-date-picker type="date"  v-model="dutyForm.etime"></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dutyFormVisible = false">取消</el-button>
        <el-button type="primary" @click.native="submitForm">提交</el-button>
      </div>
    </el-dialog>



      <!-- 当月异常顶岗数据 -->
       <div class="postChanges" style="margin:10px">
      <el-col :span="18" class="toolbar">
        <h3>当月异常顶岗数据</h3>
      </el-col>
      

      <!-- 姓名 班组  机台号 岗位  起始时间：默认取当日date-->
      <el-table ref="table" :data="postChanges.slice((currentPage-1)*pagesize2,currentPage2*pagesize2)" @selection-change="handleSelectionChangeTwo">
        <el-table-column type="selection" width="55">
        </el-table-column>
       <el-table-column prop="id" label="序号"  sortable width="90%">
        </el-table-column>
        <el-table-column prop="group" label="班组" width="100%">
        </el-table-column>
         <el-table-column prop="machineno" label="机台号" width="90%">
        </el-table-column>
         <el-table-column prop="position" label="岗位" width="120%">
        </el-table-column>
        <el-table-column prop="createTime" label="时间" width="200%">
        </el-table-column>
          <el-table-column label="操作">
          <template scope="scope">
          <el-button type="danger" @click="editPostChange(scope.$index,scope.row)" size="mini">异常修改</el-button>
        </template>
        </el-table-column>
      </el-table>
      <el-col :span="6" class="toolbar">
        <el-button type="primary" @click="batchPostCheck" size="mini">批量确认</el-button>
      </el-col>
      <el-col :span="18" class="toolbar">
       <el-pagination
            @size-change="handleSizeChange2"
            @current-change="handleCurrentChange2"
            :current-page="currentPage2"
            :page-sizes="[5, 10, 20, 40]" 
            :page-size="pagesize2"         
            layout="total, sizes, prev, pager, next, jumper"
            :total="this.postChanges.length">
      </el-pagination>
    </el-col>
       

    <!-- postForm 修改界面 -->
    <el-dialog title="修改顶岗异常" :visible.sync="postFormVisible">
      <el-form :model="postForm" label-width="100px" :rules="postFormRules" ref="postForm">
        <el-form-item label="班组">
           <el-select v-model="postForm.group" placeholder="请选择">
              <el-option
                v-for="item in groups"
                :key="item.id"
                :label="item.label"
                :value="item.id">
              </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="机台号:" prop="machines">
         <el-select multiple collapse-tags v-model='selectedArray' @change='changeSelect' placeholder='请选择'>
           <el-checkbox v-model="checked" @change='selectAll'>全选</el-checkbox>
           <el-option v-for='(item, index) in machinenos' :key='id' :label='item.label' :value='item.id'></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="岗位">
           <el-select v-model="postForm.position" placeholder="请选择">
              <el-option
                v-for="item in positions"
                :key="item.position"
                :label="item.position"
                :value="item.id">
              </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="起始时间">
          <el-date-picker type="datetime"  v-model="postForm.stime" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
        </el-form-item>
          <el-form-item label="终止时间">
          <el-date-picker type="datetime"  v-model="postForm.etime" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="postFormVisible = false">取消</el-button>
        <el-button type="primary" @click.native="submitPostForm">提交</el-button>
      </div>
    </el-dialog>



      </div>
    </div>
   </div>
</template>

<script>
  export default {

    name: 'DutyPostCheck',
    data () {
    return {

      // machinenos 选择
      selectedArray:[],

      // 通用filter查询
      filter:{
        name:'', // 用户名 
        brand:'',
        machineno:'',
       sTime:"",
       eTime:"",
       createTime:""
      },
      // 岗位
      positions:[],

      // 机台
       machinenos : [
      {"id":"1","label":"1#"},
      {"id":"2","label":"2#"},
      {"id":"3","label":"3#"},
      {"id":"4","label":"4#"},
      {"id":"5","label":"5#"},
      {"id":"6","label":"6#"},
      {"id":"7","label":"7#"},
      {"id":"8","label":"8#"},
      {"id":"9","label":"9#"},
      {"id":"10","label":"10#"},
      {"id":"11","label":"11#"},
      {"id":"12","label":"12#"},
      {"id":"13","label":"13#"},
      {"id":"14","label":"14#"},
      {"id":"15","label":"15#"},
      {"id":"16","label":"16#"},
      {"id":"17","label":"17#"},
      {"id":"18","label":"18#"},
      {"id":"19","label":"19#"},
      {"id":"20","label":"20#"},
      {"id":"21","label":"21#"},
      {"id":"22","label":"22#"},
      {"id":"23","label":"23#"},
      {"id":"24","label":"24#"},
      {"id":"25","label":"25#"},
      {"id":"26","label":"26#"},
      {"id":"27","label":"27#"},
      {"id":"28","label":"28#"},
      {"id":"29","label":"29#"},
      {"id":"30","label":"30#"}
      ],

      // 请假类型
      absTypes:[
        {"id":"1","label":"休息"},
        {"id":"2","label":"出差"},
        {"id":"3","label":"病假"},
        {"id":"4","label":"事假"},
        {"id":"5","label":"产假"},
        {"id":"6","label":"丧假"},
        {"id":"7","label":"探亲假"},
        {"id":"8","label":"陪护假"},
        {"id":"9","label":"年假"},
        {"id":"10","label":"补休（有补休单）"},
        {"id":"11","label":"补休（无补休单）"}],

         // 班组
      groups:[
        {"id":"1","label":"甲班"},
        {"id":"2","label":"乙班"},
        {"id":"3","label":"丙班"},
        {"id":"4","label":"丁班"},
        {"id":"5","label":"细甲"},
        {"id":"6","label":"细乙"},
        {"id":"7","label":"细丙"},
        {"id":"8","label":"细丁"}],
      
      selections: [],  // 列表选中列
      postSelects: [],  // post岗位列表选中列
      addLoading:false,
      // 编辑考勤异常数据 
      dutyForm: {
        id: 0,
        absId: '',
        stime: '',
        etime:''
      },
      dutyFormVisible:false,
      dutyFormRules: {
        absType: [
          { required: true, message: '填写不能为空', trigger: 'blur' }
        ],
        sTime: [
          { required: true, message: '填写不能为空', trigger: 'blur' }
        ],
        sTime: [
          { required: true, message: '填写不能为空', trigger: 'blur' }
        ],
      },

      // 编辑岗位异常数据  【班组、机台、岗位、时间】
      postForm: {
        id: 0,
        group: '',
        machineno: '',
        pid:'',
        createTime:''
      },
      postFormVisible:false,
      postFormRules: {
        group: [
          { required: true, message: '填写不能为空', trigger: 'blur' }
        ],
        machineno: [
          { required: true, message: '填写不能为空', trigger: 'blur' }
        ],
        pid: [
          { required: true, message: '填写不能为空', trigger: 'blur' }
        ],
        createTime: [
          { required: true, message: '填写不能为空', trigger: 'blur' }
        ],
      },
      
     //分页
      currentPage:1, //初始页
      pagesize:5,    //每页的数据
    
       //分页2
      currentPage2:1, //初始页
      pagesize2:5,    //每页的数据

       // 个人月考勤异常数据
      dutyExcepts:[],
      // 个人月顶岗数据
      postChanges:[]
    }
  },
  methods:{

     // 设置修改后机台号
    setPostFormData:function()
    {
        var temp = this.selectedArray;
       this.postForm.machineno = temp.join(","); // [] 转换成字符串
    },

    changeSelect(val) {
      console.log(val.length)
        if (val.length === this.machinenos.length) {
        this.checked = true
        } else {
        this.checked = false
        }
      },
       selectAll() {
        this.selectedArray = []
        if (this.checked) {
        this.machinenos.map((item) => {
         this.selectedArray.push(item)
        })
        console.log(this.selectedArray)
        } else {
        this.selectedArray = []
        }
       },

    initFilter()
    {
      this.filter.name = sessionStorage.getItem('user');
    },
    editDutyItem:function(index, row){
      this.dutyFormVisible = true
      this.dutyForm = Object.assign({}, row)
    },

     // 编辑
    submitForm: function () {
      this.$refs.dutyForm.validate((valid) => {
        if (valid) {
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            this.editLoading = true
            let params = Object.assign({}, this.dutyForm)
            console.log(params)
            this.$api.dutyExcept.askUpdateDuty(params).then((res) => {
              if(res.code == 200) {
                this.$message({ message: '操作成功', type: 'success' })
                this.dutyFormVisible = false
                this.$refs['dutyForm'].resetFields()
                this.findDutyExcept(); // 刷新数据
              } else {
                this.$message({message: '操作失败, ' + res.msg, type: 'error'})
              }
            
            })
          })
        }
      })
    },
    // 批量选择修改
     handleSelectionChange: function (selections) {
      this.selections = selections
      console.log(this.selections)
      this.$emit('selectionChange', {selections:selections})
    },
    // 批量选择修改
     handleSelectionChangeTwo: function (selections) {
      this.postSelects = selections
      console.log(this.postSelects)
      this.$emit('selectionChange', {selections:postSelects})
    },
    // 批量考勤确认
    batchDutyCheck:function()
    {
     this.$api.dutyExcept.confirmDutyExcept(this.selections).then((res) => {
         console.log(res.data)
        if(res.code == 200) {
          this.findDutyExcept();//fresh
        this.$message({message: '提交成功', type: 'success'})
      } else {
        this.$message({message: '操作失败, ' + res.msg, type: 'error'})
       }
     }).catch(() => {
      })
    },

    // 个人考勤数据查询
    findDutyExcept:function()
    {
      this.initFilter();
     this.$api.dutyExcept.findDutyExcept(this.filter)
            .then((res) => {
             console.log(res.data)
            this.dutyExcepts = res.data
          })
    },
    getPositions:function()
      {
         this.$api.basicPosition.findAll().then((res) => {
         console.log(res.data);
          this.positions = res.data;
        })
      },
      

      // 个人顶岗数据查询
    findPostChange:function()
    {
      this.initFilter();
     this.$api.position.findPostChange(this.filter)
            .then((res) => {
             console.log(res.data)
            this.postChanges = res.data
          })
    },
   // 批量顶岗确认
    batchPostCheck:function()
    {
     this.$api.position.confirmPostExcept(this.postSelects).then((res) => {
         console.log(res.data)
        if(res.code == 200) {
          this.findPostChange();//fresh
        this.$message({message: '提交成功', type: 'success'})
      } else {
        this.$message({message: '操作失败, ' + res.msg, type: 'error'})
       }
     }).catch(() => {
      })
    },


    editPostChange:function(index, row){
      this.postFormVisible = true
      this.postForm = Object.assign({}, row)
    },

     // 编辑
    submitPostForm: function () {
      this.$refs.postForm.validate((valid) => {
        if (valid) {
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            this.setPostFormData(); // 设置修改后机台号
            let params = Object.assign({}, this.postForm)
            console.log(params)
            this.$api.position.askUpdatePost(params).then((res) => {
              if(res.code == 200) {
                this.$message({ message: '操作成功', type: 'success' })
                this.postFormVisible = false
                this.$refs['postForm'].resetFields()
                this.findPostChange()
              } else {
                this.$message({message: '操作失败, ' + res.msg, type: 'error'})
              }
            
            })
          })
        }
      })
    },
    
    // 查询产量定额信息
    getRegOutput:function()
    {
      this.$http.get("http://localhost:3000/dutyExcepts")
          .then(function(response){
            
            this.dutyExcepts = response.body;
            console.log( this.dutyExcepts);
          })
    },

    // 初始页currentPage、初始每页数据数pagesize和数据data
    handleSizeChange:function(size) {
      this.pagesize = size;
      console.log(this.pagesize)  //每页下拉显示数据
    },
    handleCurrentChange:function(currentPage){
      this.currentPage = currentPage;
      console.log(this.currentPage)  //点击第几页
    },
    // 初始页currentPage、初始每页数据数pagesize和数据data
    handleSizeChange2:function(size) {
      this.pagesize2 = size;
      console.log(this.pagesize2)  //每页下拉显示数据
    },
    handleCurrentChange2:function(currentPage){
      this.currentPage2 = currentPage;
      console.log(this.currentPage2)  //点击第几页
    }

  },
  created()
  {
     // 岗位下拉初始化
       this.getPositions()
    this.findDutyExcept()
    this.findPostChange();
    // this.getRegOutput();
  }
  }
</script>

<style lang="scss">
  .site-wrapper.site-page--not-found {
    position: absolute;
    top: 60px;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    .site-content__wrapper {
      padding: 0;
      margin: 0;
      background-color: #fff;
    }
    .site-content {
      position: fixed;
      top: 15%;
      left: 50%;
      z-index: 2;
      padding: 30px;
      text-align: center;
      transform: translate(-50%, 0);
    }
    .not-found-title {
      margin: 20px 0 15px;
      font-size: 8em;
      font-weight: 500;
      color: rgb(55, 71, 79);
    }
    .not-found-desc {
      margin: 0 0 30px;
      font-size: 26px;
      text-transform: uppercase;
      color: rgb(118, 131, 143);
      > em {
        font-style: normal;
        color: #ee8145;
      }
    }
    .not-found-btn-gohome {
      margin-left: 30px;
    }
  }
</style>



