<template>
  <div class="page-container">  

 <div class="toolbar" style="margin-top:30px;">

     <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filter" ref="filterForm">
        <el-form-item label="起始:">
         <el-date-picker type="date" placeholder="选择日期" v-model="filter.sTime" style="width: 100%;" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
        </el-form-item>
       <el-form-item label="终止:">
         <el-date-picker type="date" placeholder="选择日期" v-model="filter.eTime" style="width: 100%;" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="getSalarySearch">查询</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </el-col>
</div>
<div>
    <!-- 查询数据显示 序号，时间，产量，工资-->
    <el-table ref="table" border :data="salResTotal.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="margin-left:50px">
    <el-table-column type="index" width="50" label="序号">
      </el-table-column>
      <el-table-column prop="createTime" label="时间"  width="180%">
      </el-table-column>
      <el-table-column prop="group" label="班组" width="90%"> 
      </el-table-column>
      <el-table-column prop="brand" label="牌号" width="150%">
      </el-table-column>
      <el-table-column prop="machineno" label="生产机台" width="90%">
      </el-table-column>
      <el-table-column prop="totalAmt" label="产量(万支)" width="90%">
      </el-table-column>
       <el-table-column prop="groupAmt" label="班总(万支)" width="90%">
      </el-table-column>
      <el-table-column prop="amtSalary" label="日工资"  width="90%">
      </el-table-column>
    </el-table>
  <el-row>
    <el-col :span="12"><span style="color:red">合计：{{totalSalary}}</span></el-col>
    <el-col :span="12"><el-pagination
    @size-change="handleSizeChange"
    @current-change="handleCurrentChange"
    :current-page="currentPage"
    :page-sizes="[5, 10, 20, 40]" 
    :page-size="pagesize"        
    layout="total, sizes, prev, pager, next, jumper"
    :total="salResTotal.length"> 
    </el-pagination></el-col>
  </el-row>
  <!-- 分页 -->
    
</div>
</div>
</template>

<script>
import Cookies from "js-cookie";
  export default {
    name: 'SalaryTime',
    data () {
    return {
    
      //分页
      currentPage:1, //初始页
      pagesize:40,    //每页的数据
      empInfo:{},
      // // 通用filter
      filter:{
        name:'', // 用户名 
       sTime:"",
       eTime:""
      },
      // 工资查询返回
      salResTotal:[],
      // 月工资合计
      totalSalary:0, 
    }
  },
    methods:{
      initFilter()
      {
        this.filter.name = sessionStorage.getItem('user');
        console.log(this.filter);
      },
      
      // 默认初始选择当月第一天作为起始时间，当前作为终止时间
      getSalarySearch:function()
      {
         // this.$http.get("http://localhost:3000/salaryAmtTotal")
         //  .then(function(response){
            
         //    this.salResTotal = response.body;
         //    console.log( this.salResTotal);
         //  })
         console.log(this.filter)
          this.$api.amtCounter.findAmtSalary(this.filter).then((res) => {
          // 处理异常
            if(res.msg)
            {
              this.$message({message: '操作失败, ' + res.msg, type: 'error'})
            }
           console.log(res.data)
            this.salResTotal = res.data
            // 求合计
            this.setSalResTotal();
        })
      },
      // 计算合计工资
      setSalResTotal:function()
      {
        this.totalSalary = 0;
        for (var i = this.salResTotal.length - 1; i >= 0; i--) {
          if(this.salResTotal[i].amtSalary)
          {
             this.totalSalary += this.salResTotal[i].amtSalary;
          }
        }
      },
       // 初始页currentPage、初始每页数据数pagesize和数据data
    handleSizeChange:function(size) {
      this.pagesize = size;
      console.log(this.pagesize)  //每页下拉显示数据
    },
    handleCurrentChange:function(currentPage){
      this.currentPage = currentPage;
      console.log(this.currentPage)  //点击第几页
    },
    reset() {
        this.$refs.filterForm.resetFields()
      }
    },
    created(){
      this.initFilter();
      this.getSalarySearch();
    }
  }
</script>

<style lang="scss">



</style>



