<template>
  <div class="container">
    
      <div class="site-content" style="float:left;padding-top:5px;">
        <span style="font-size:xx-large">
          <el-link style="font-size:xx-large" type="primary" href="http://10.162.91.8:8088/cigOutputInfo/downDayAmtTmp" target="_blank">模板文件下载</el-link>
        </span>
       
      </br>
      <div style="margin:20px"> 
         生产时间：<el-date-picker type="date" v-model="createTime" placeholder="选择日产量创建时间" value-format=" yyyy-MM-dd" format="yyyy-MM-dd"></el-date-picker>
      </div>

      <el-upload
          class="upload-demo"
          drag
          action="http://10.162.91.8:8088/cigOutputInfo/upDayAmtExecl"
          :multiple = "false"
          accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel"
          :file-list="file"
          :auto-upload="true"
          :before-upload="onBeforeUpload"
          :on-success="handlesuccess"
          :data="{createTime:this.createTime}">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">请根据模板EXECL，上传当天产量数据文档EXECL</div>
        </el-upload>
        <el-button type="primary" @click="calDaySalary">一键计薪</el-button>
      </div>
      <!--  <el-button @click="$router.go(-1)">返回上一页</el-button>
        <el-button type="primary" class="not-found-btn-gohome" @click="$router.push('/')">进入首页</el-button> -->
    </div>
</template>

<script>
import { upDownUrl } from '@/utils/global'
  export default {

  data () {
    return {
      name: 'DayAmtMgt',
      downloadUrl:upDownUrl+'/cigOutputInfo/downDayAmtTmp',
      uploadUrl:upDownUrl+'/cigOutputInfo/upDayAmtExecl',
      // param:{
      //   createTime:'',
      //   file:'',
      // },
      file:'',//此数组中存入所有提交的文档信息
      createTime:'',// 产量表创建时间

    }
  },
   methods:{

    // 根据日产量计薪入库
    calDaySalary:function()
    {
      // 入参检查
        if(!this.createTime)
        {
          this.$message({message: '请先选择生产时间或上传日产量数据',type: 'error'})
          return;
        } 
        this.$api.amtCounter.calAmtSalary({"dayTime":this.createTime}).then(response => {
        console.log(response)
         if(response.code === 200){
         console.log('薪资计算入库成功')
          this.$message({message: '薪资计算入库成功',type: 'success'})
       }else{
          console.log('操作失败')
           this.$message({message: '操作失败, ' + response.msg, type: 'error'})
         }
      })
    },
    onBeforeUpload:function()
    {
      // 选择时间
      if(!this.createTime)
      {
        this.$message({message: '上传失败,请先选择生产时间!', type: 'error'})
      }
    },
     customUpload(file) {
      // this.generatorFileMd5(file.file)
      // 自定义上传
      this.$api.fileUpDown.upDayAmtExecl(this.file).then(response => {
        console.log(response)
         if(response.code === 200){
         console.log('解析成功')
          this.$message({message: '上传成功,对应数据生产时间：' + this.createTime ,type: 'success'})
       }else{
          console.log('解析失败')
           this.$message({message: '上传失败, ' + response.msg, type: 'error'})
         }

      })
    },
      
      handlesuccess(response, file){
      //response即为后台返回的全部内容
       if(response.code === 200){
         console.log('解析成功')
        this.$message({message: '上传成功,对应数据生产时间：' + this.createTime ,type: 'success'})
       }else{
          console.log('解析失败')
           this.$message({message: '上柴失败, ' + response.msg, type: 'error'})
         }
       }
       // handleError(response, file)
       // {
       //  if(response.msg != null){
       //    console.log('解析失败')
       //     this.$message({message: '上柴失败, ' + res.msg, type: 'error'})
       //   }
       
       // }

   }
}
</script>

<style lang="scss">
 
</style>



