<template>
  <div class="page-container">
  <!--工具栏-->
  <div class="toolbar" style="margin-top:30px;float:left;padding-top:10px;padding-left:15px;">
    <el-form :inline="true" :model="filters" :size="size">
      <el-form-item>
        <el-input v-model="filters.name" placeholder="角色名"></el-input>
      </el-form-item>
      <el-form-item>
        <kt-button icon="fa fa-search" label="搜索" perms="sys:role:view" type="primary" @click="findAll"/>
      </el-form-item>
      <el-form-item>
        <kt-button icon="fa fa-plus" label="新增" perms="sys:role:add" type="primary" @click="handleAdd" />
      </el-form-item>
    </el-form>
  </div>

   <!-- 查询数据显示 序号，时间，产量，工资 单选表格-->
    <el-table ref="table" highlight-current-row :data="rolesResult.slice((currentPage-1)*pagesize,currentPage*pagesize)"  @current-change="handleSelectChange">
     <el-table-column prop="id" label="序号" width="100%">
      </el-table-column>
      <el-table-column prop="name" label="角色名" width="100%">
      </el-table-column>
      <el-table-column prop="remark" label="备注" width="100%">
      </el-table-column>
      <el-table-column prop="createBy" label="创建人" width="100%">
      </el-table-column>
      <el-table-column prop="createTime" label="创建时间">
      </el-table-column>
      <el-table-column label="操作">
         <template scope="scope">
          <el-button type="primary" @click="handleEdit(scope.$index,scope.row)" size="mini">修改</el-button>
          <el-button type="danger" @click="handleDelete(scope.$index,scope.row)" size="mini">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 20, 40]" 
            :page-size="pagesize"         
            layout="total, sizes, prev, pager, next, jumper"
            :total="rolesResult.totalSize">
      </el-pagination>
  <!-- </el-col> -->
  <!--新增编辑界面-->
  <el-dialog :title="operation?'新增':'编辑'" width="40%" :visible.sync="dialogVisible" :close-on-click-modal="false">
    <el-form :model="dataForm" label-width="80px" :rules="dataFormRules" ref="dataForm" :size="size">
      <el-form-item label="ID" prop="id" v-if="false">
        <el-input v-model="dataForm.id" :disabled="true" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="角色名" prop="name">
        <el-input v-model="dataForm.name" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="备注 " prop="remark">
        <el-input v-model="dataForm.remark" auto-complete="off" type="textarea"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button :size="size" @click.native="dialogVisible = false">取消</el-button>
      <el-button :size="size" type="primary" @click.native="submitForm" :loading="editLoading">提交</el-button>
    </div>
  </el-dialog>

    <!--角色菜单，表格树内容栏-->
  <div class="menu-container" :v-if="true">
    <div class="menu-header">
      <span><B>角色菜单授权</B></span>
    </div>
    <el-tree :data="menuData" size="mini" show-checkbox node-key="id" :props="defaultProps"
      style="width: 100%;pading-top:20px;" ref="menuTree" :render-content="renderContent"
      v-loading="menuLoading" element-loading-text="拼命加载中" :check-strictly="true"
      @check-change="handleMenuCheckChange">
    </el-tree>
    <div style="float:left;padding-left:24px;padding-top:12px;padding-bottom:4px;">
      <el-checkbox v-model="checkAll" @change="handleCheckAll" :disabled="this.selectRole.id == null"><b>全选</b></el-checkbox>
    </div>
    <div style="float:right;padding-right:300px;padding-top:4px;padding-bottom:4px;">
      <kt-button label="重置" perms="sys:role:edit" type="primary" @click="resetSelection" 
        :disabled="this.selectRole.id == null"/>
      <kt-button label="提交" perms="sys:role:edit" type="primary" @click="submitAuthForm" 
        :disabled="this.selectRole.id == null" :loading="authLoading"/>
    </div>
  </div>
  </div>
</template>

<script>
import KtTable from "@/views/Core/KtTable"
import KdTable from "@/views/Core/KdTable"
import KtButton from "@/views/Core/KtButton"
import TableTreeColumn from '@/views/Core/TableTreeColumn'
import { format } from "@/utils/datetime"
export default {
  components:{
    KdTable,
    KtTable,
    KtButton,
    TableTreeColumn
  },
  data() {
    return {
    //分页
      currentPage:1, //初始页
      pagesize:5,    //每页的数据
      size: 'small',
      filters: {
        name: ''
      },
   
      rolesResult:[],

      operation: false, // true:新增, false:编辑
      dialogVisible: false, // 新增编辑界面是否显示
      editLoading: false,
      dataFormRules: {
        name: [
          { required: true, message: '请输入角色名', trigger: 'blur' }
        ]
      },
      // 新增编辑界面数据
      dataForm: {
        id: 0,
        name: '',
        remark: ''
      },
      selectRole: {},
      menuData: [],
      menuSelections: [],
      menuLoading: false,
      authLoading: false,
      checkAll: false,
      currentRoleMenus: [],
      defaultProps: {
        children: 'children',
        label: 'name'
      }
    }
  },
  methods: {

    // table单选
    handleSelectChange(val) {
      console.log(val);
        this.selectRole = val;
      },
    // 初始页currentPage、初始每页数据数pagesize和数据data
    handleSizeChange:function(size) {
      this.pagesize = size;
      console.log(this.pagesize)  //每页下拉显示数据
    },
    handleCurrentChange:function(currentPage){
      this.currentPage = currentPage;
      console.log(this.currentPage)  //点击第几页
    },
    // 获取全部数据
    findAll: function (data) {
      
      this.$api.role.findAll().then((res) => {
        this.rolesResult = res.data
        console.log(this.rolesResult)
        this.findTreeData()
      })
    },    
    // findAll: function (data) {
    //   if(data !== null) {
    //     this.pageRequest = data.pageRequest
    //   }
    //   this.pageRequest.columnFilters = {name: {name:'name', value:this.filters.name}}
    //   this.$api.role.findAll().then((res) => {
    //     this.rolesResult = res.data
    //     this.findTreeData()
    //   }).then(data!=null?data.callback:'')
    // },
    // 删除
    handleDelete: function (index,row) {
      this.$confirm('确认删除选中记录吗？', '提示', {
        type: 'warning'
      }).then(() => {
        console.log("删除"+row.id)
          this.delete(row.id)
      })
    },
    // 删除操作
    delete: function (ids) {
        let params = []
        let idArray = (ids+'').split(',')
        for(var i=0; i<idArray.length; i++) {
          params.push({'id':idArray[i]})
        }
        this.$api.role.batchDelete(params).then((res) => {
            console.log(res.data)
            if(res.code == 200) {
            this.$message({message: '操作成功', type: 'success'})
            this.findAll() // 刷新数据
            } else { 
            this.$message({message: '操作失败, ' + res.msg, type: 'error'})
           }
         }).catch(() => {
          })
      },
    // 显示新增界面
    handleAdd: function () {
      this.dialogVisible = true
      this.operation = true
      this.dataForm = {
        id: 0,
        name: '',
        remark: ''
      }
    },
    // 显示编辑界面
    handleEdit: function (index, row) {
      this.dialogVisible = true
      this.operation = false
      this.dataForm = Object.assign({}, row)
    },
    // 编辑
    submitForm: function () {
      this.$refs.dataForm.validate((valid) => {
        if (valid) {
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            this.editLoading = true
            let params = Object.assign({}, this.dataForm)
            this.$api.role.save(params).then((res) => {
              this.editLoading = false
              if(res.code == 200) {
                this.findAll()
                this.$message({ message: '操作成功', type: 'success' })
                this.dialogVisible = false
                this.$refs['dataForm'].resetFields()
              } else {
                this.$message({message: '操作失败, ' + res.msg, type: 'error'})
              }
            })
          })
        }
      })
    },
    // 获取数据
    findTreeData: function () {
      this.menuLoading = true
      this.$api.menu.findMenuTree().then((res) => {
        this.menuData = res.data
        this.menuLoading = false
      })
    },
    // 角色选择改变监听
    handleRoleSelectChange(val) {
      if(val == null || val.val == null) {
        return
      }
      this.selectRole = val.val
      this.$api.role.findRoleMenus({'roleId':val.val.id}).then((res) => {
        this.currentRoleMenus = res.data
        this.$refs.menuTree.setCheckedNodes(res.data)
      })
    },
    // 树节点选择监听
    handleMenuCheckChange(data, check, subCheck) {
      if(check) {
        // 节点选中时同步选中父节点
        let parentId = data.parentId
        this.$refs.menuTree.setChecked(parentId, true, false)
      } else {
        // 节点取消选中时同步取消选中子节点
        if(data.children != null) {
          data.children.forEach(element => {
            this.$refs.menuTree.setChecked(element.id, false, false)
          });
        }
      }
    },
    // 重置选择
    resetSelection() {
      this.checkAll = false
      this.$refs.menuTree.setCheckedNodes(this.currentRoleMenus)
    },
    // 全选操作
    handleCheckAll() {
      if(this.checkAll) {
        let allMenus = []
        this.checkAllMenu(this.menuData, allMenus)
        this.$refs.menuTree.setCheckedNodes(allMenus)
      } else {
        this.$refs.menuTree.setCheckedNodes([])
      }
    },
    // 递归全选
    checkAllMenu(menuData, allMenus) {
      menuData.forEach(menu => {
        allMenus.push(menu)
        if(menu.children) {
          this.checkAllMenu(menu.children, allMenus)
        }
      });
    },
    // 角色菜单授权提交
    submitAuthForm() {
      let roleId = this.selectRole.id
      if('admin' == this.selectRole.name) {
        this.$message({message: '超级管理员拥有所有菜单权限，不允许修改！', type: 'error'})
        return
      }
      this.authLoading = true
      let checkedNodes = this.$refs.menuTree.getCheckedNodes(false, true)
      let roleMenus = []
      for(let i=0, len=checkedNodes.length; i<len; i++) {
        let roleMenu = { roleId:roleId, menuId:checkedNodes[i].id }
        roleMenus.push(roleMenu)
      }
      this.$api.role.saveRoleMenus(roleMenus).then((res) => {
        if(res.code == 200) {
          this.$message({ message: '操作成功', type: 'success' })
        } else {
          this.$message({message: '操作失败, ' + res.msg, type: 'error'})
        }
        this.authLoading = false
      })
    },
    renderContent(h, { node, data, store }) {
      return (
      <div class="column-container">
        <span style="text-algin:center;margin-right:80px;">{data.name}</span>
        <span style="text-algin:center;margin-right:80px;">
          <el-tag type={data.type === 0?'':data.type === 1?'success':'info'} size="small">
            {data.type === 0?'目录':data.type === 1?'菜单':'按钮'}
          </el-tag>
        </span>
        <span style="text-algin:center;margin-right:80px;"> <i class={data.icon}></i></span>
        <span style="text-algin:center;margin-right:80px;">{data.parentName?data.parentName:'顶级菜单'}</span>
        <span style="text-algin:center;margin-right:80px;">{data.url?data.url:'\t'}</span>
      </div>);
        },
    // 时间格式化
        dateFormat: function (row, column, cellValue, index){
            return format(row[column.property])
        }
    
  },
  created()
  {
     this.findAll() 
  },
  mounted() {
  }
}
</script>
<style scoped>
.menu-container {
  margin-top: 10px;
}
.menu-header {
  padding-left: 8px;
  padding-bottom: 5px;
  text-align: left;
  font-size: 16px;
  color: rgb(20, 89, 121);
  
}
.el-table .select-row {
    background: #f0f9eb;
  }
</style>


