<template>
  <div class="book-container">  
   <span style="color:red">请先选择预约会议室：
     <el-select v-model="roomId" placeholder="请选择会议室">
      <el-option
      v-for="item in roomOptions"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
</span>
<vue-scheduler 
:time-range="[8,20]"
:available-views="['week', 'day','month']"
initial-view="day"
:initial-date="new Date()"
:events="events"
:event-display="eventDisplay"
:event-dialog-config="dialogConfig"
@month-changed="monthChanged"
@event-clicked="eventClicked"
@event-created="eventCreated"/>

<el-dialog title="会议预定详情" :visible.sync="dialogTableVisible" width="100%">

</el-dialog>
</div>
</template>




<script>
  export default {
    data() {
      return {
        events: [],
        roomId:"1",// 默认值
        roomString:'',
        roomOptions:[
        {value:'1',label:'卷包车间党员之家'},
        {value:"2",label:"卷包车间一楼大师工作室"},
        {value:"3",label:"卷包车间二楼大师工作室"},
        {value:"4",label:"卷包车间三楼连廊"}],
        // 会议室305 会议主题 日期、开始时间、结束时间、预约人、联系电话、备注
        dialogConfig: {
          title: '预定会议室',
          createButtonLabel: '预约',
          enableTimeInputs: true,

          fields: [
          {
            name: 'topic',
              // required: true,
              label: '会议主题',
              value:'会议主题'
            },
            {
              name: 'createUser',
              // required: true,
              label: '预约人',
              value:'admin'
            },
            {
              name: 'telephone',
              // required: true,
              label: '预约人电话'
            },
            {
              name: 'remark',
              type: 'textarea',
              label: '备注说明'
            }
            ]
          }
        }
      },
      methods: {

        // 初始化Events
        initEvents()
        {
          console.log("初始化events....")
          this.initRoomString();
          // 根据RoomId,查询events
          this.queryReserve(this.roomId)
        },
        // 查询一周预定events....
        queryReserve(roomId)
        {
          console.log("查询一周预定events...."+roomId)
          this.$api.bookRoom.findByRoom(roomId).then((res) => {
            this.events = res.data
            console.log(this.events)
          })
        },

        initRoomString()
        {
          var num = this.roomId-1;
          console.log(this.roomOptions[num].label);
          this.roomString = "预定"+this.roomOptions[num].label;
          this.dialogConfig.title = this.roomString;
        },

        eventClicked(event) {

          console.log('Event clicked');

          console.log(event);

        },

        monthChanged(newDate) {
          console.log('Month Changed');
        },

        eventDisplay(event) {
         console.log(event);
         var tmp = "主题："+event.topic +'\n' 
         + "创建人："+event.createUser +'\n' 
         + "联系电话："+event.telephone+'\n';
         if(event.comments != null)
         {
          tmp+= "备注信息："+event.comments;
        }
        var displayValue = tmp;

           // console.log(displayValue);
           return displayValue
         }
       },
       created()
       {
        this.initEvents() 
      },
      updated()
      {
        this.initEvents() 
      },
      mounted() {
      }
    }

  </script>

  <style lang="scss">
    .book-container {
      position: absolute;
      top: 40px;
      bottom: 0px;
      left: 10px;
      right: 0px;
      text-align:left;
      width:85%;
      font-size:large;

    }


    .cell-red{
      background: red;
    }
  </style>
 


