<template>
  <div class="container">
  <!--工具栏-->
      <div class="searchbox">
      <el-autocomplete
           v-model="optionSearch"
           :fetch-suggestions="accounQuerySearchAsync"
           @select="accountHandleSelect"
           :debounce=10
            placeholder="请输入姓名"
       >
      </el-autocomplete>
      <el-button @click="accountFind">搜索</el-button>
    </div>
    <div class = "fileUpload">
      <el-form-item label="选择模板" :label-width="formLabelWidth" :required="trueFlag">
                        <el-row>
                            <el-col :span="12">
                                <div class="grid-content bg-purple">
                                    <el-upload
                                            class="upload-demo"
                                            ref="upload"
                                            :action="actionUrl"
                                            :auto-upload="false"
                                            :limit="10"
                                            multiple
                                            accept="application/execl"
                                            :before-upload="beforeUpload"
                                            :before-remove="beforeRemove"
                                            :on-remove="handleRemove"
                                            :on-change="fileChange"
                                            :file-list="fileList"
                                            :drag="trueFlag">
                                        <el-button size="small" type="primary">选取模板</el-button>
                                        <!--<el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>-->
                                        <div slot="tip" class="el-upload__tip">只能上传pdf文件，且不超过2M</div>
                                    </el-upload>
                                </div>
                            </el-col>
                        </el-row>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button v-on:click="uploadDialogFormVisible = false">关闭</el-button>
                    <!--<el-button type="primary" v-on:click="uploadDialogFormVisible = false">上传</el-button>-->
                    <el-button type="primary" @click="submitUpload">上传到服务器</el-button>
                </div>
            </el-dialog>

    </div>
    <div class = "fileDownload">
      
    </div>

  </div>
</template>

<script>

export default {
 
  data() {
    return {
      fileList: [{name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}],
       optionSearch: "",
       options:[],
       empPostInfos:[]         
    }
  },
  methods: {

     handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(`确定移除 ${ file.name }？`);
      },
    getInitData:function()
    {
       this.$http.get("http://localhost:3000/empPostInfo")
          .then(function(response){
             console.log(response.body);
            this.empPostInfos = response.body;
             this.options = this.empPostInfos;

          })
    },
   //queryString 为在框中输入的值    callback 回调函数,将处理好的数据推回
    accounQuerySearchAsync(queryString, callback) {
         let list = [{}];
         let param = {
              pageNum: 1,
              pageSize: 9999,
              key: queryString
         }      
         
//      let data = this.getAccountUserInfo(param,0);
        this.options.forEach(function(item,i){
              if(item.name.indexOf(queryString) != -1){
                    list.push({"value":item.name});   
              }      
        })
        if(!queryString){
              list = list.splice(0,7);
        }
        callback(list);
    },
    // 模糊查询下拉框 鼠标键盘选中点击触发
    accountHandleSelect(item) { 
         this.optionSearch = item.value;
    } 
  },
  created(){
    this.getInitData()
  },
  mounted() {
  }
}
</script>
<style scoped>
.menu-container {
  margin-top: 10px;
}
.menu-header {
  padding-left: 8px;
  padding-bottom: 5px;
  text-align: left;
  font-size: 16px;
  color: rgb(20, 89, 121);
  
}
</style>


