<template>
  <!-- 点值 table 班组月上班天数（需要每月维护修改） -->
  <div class="pagecontainer"> 

      <div style="float:left;margin-left:40px;margin-right:40px;padding-top:5px;">
      <div>
      选择月份：<el-date-picker type="month" v-model="monthTime" placeholder="选择查询月份" value-format=" yyyy-MM-dd" format="yyyy-MM-dd"></el-date-picker>
      <el-button type="primary" @click="getGroupWorkTime" size="small">查询</el-button> 
       <el-button type="primary" @click="addGroupWorkTime" size="small">新增</el-button> 
      </div>
      <div><span>当前表格数据时间：{{monthDate}}</span></div>
     <split-table :headData="headData" :bodyData="bodyData" :operFlag="false" :checkFlag="false" @editData="editData">
    </split-table>
     <el-button type="primary" @click="updateWorkTime" size="small">保存表格修改</el-button> <span>数据最后更新时间：{{lastTime}}</span>
    </div>

  <!--新增界面-->
  <el-dialog title="新增" width="500px" :visible.sync="dataFormVisible" :close-on-click-modal="false">
    <el-form :model="workTimeForm" label-width="120px" :ref="workTimeForm"> 
      <el-form-item label="请新增数据月份">
      <el-date-picker type="month" v-model="workTimeForm.monthTime" value-format="yyyy-MM-dd" format="yyyy-MM-dd"></el-date-picker>
        </el-form-item>
         <el-form-item label="当月点值">
          <el-input v-model="workTimeForm.pointVal"></el-input>
        </el-form-item>
        <el-form-item label="甲班">
          <el-input v-model="workTimeForm.workDays[0]"></el-input>
        </el-form-item>
       <el-form-item label="乙班">
          <el-input v-model="workTimeForm.workDays[1]"></el-input>
        </el-form-item>
        <el-form-item label="丙班">
          <el-input v-model="workTimeForm.workDays[2]"></el-input>
        </el-form-item>
        <el-form-item label="丁班">
          <el-input v-model="workTimeForm.workDays[3]"></el-input>
        </el-form-item>
        <el-form-item label="细甲">
          <el-input v-model="workTimeForm.workDays[4]"></el-input>
        </el-form-item>
        <el-form-item label="细乙">
          <el-input v-model="workTimeForm.workDays[5]"></el-input>
        </el-form-item>
        <el-form-item label="细丙">
          <el-input v-model="workTimeForm.workDays[6]"></el-input>
        </el-form-item>
        <el-form-item label="细丁">
          <el-input v-model="workTimeForm.workDays[7]"></el-input>
        </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button :size="size" @click.native="dataFormVisible = false">取消</el-button>
      <el-button :size="size" type="primary" @click.native="submitForm" :loading="addLoading">提交</el-button>
    </div>
  </el-dialog>

  <div  style="margin-top:50px;padding-top:100px;">
        <span style="font-size:xx-large">
          <el-link style="font-size:xx-large" type="primary" href="http://10.162.91.8:8088/cigOutputInfo/downDayAmtTmp" target="_blank">月工资明细模板文件下载</el-link>
        </span>
       
      </br>
      <div style="margin:20px"> 
         选择月份：<el-date-picker type="month" v-model="createTime" placeholder="选择工资明细时间" value-format=" yyyy-MM-dd" format="yyyy-MM-dd"></el-date-picker>
      </div>

      <el-upload
          class="upload-demo"
          drag
          action="http://10.162.91.8:8088/cigOutputInfo/upDayAmtExecl"
          :multiple = "false"
          accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel"
          :file-list="file"
          :auto-upload="true"
          :before-upload="onBeforeUpload"
          :on-success="handlesuccess"
          :data="{createTime:this.createTime}">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">请根据模板EXECL，上传当天产量数据文档EXECL</div>
        </el-upload>
       
      </div>

  </div>

</template>

<script>
import Cookies from "js-cookie";
import store from '@/store'
 import SplitTable from 'vue-split-table';
  export default {
    components: { SplitTable },
    data () {
      return {
        // 新增数据
        dataFormVisible:false,
        workTimeForm:{
          groups:["甲班","乙班","丙班","丁班","细甲","细乙","细丙","细丁"],
          workDays:[],
          monthTime:'',
          pointVal:'',
        },
        size:"small",
        addLoading:false,
        file:'',
        createTime:'',
        monthTime:'',// 用于条件查询
        monthDate:'',
        // 上班时间最后更新时间
        lastTime:"",
        // 班组工作时间
        groupWorkTime:[], // {}
        pointVal:"",
        headData: ["当月点值","班组", "月上班天数"],
        bodyData: [{ pointVal:[],groups: [], days: []}], 
        updateData:[]
      }
    },
    methods: {

    // 新增
    addGroupWorkTime:function()
    {
      this.dataFormVisible = true;
    },
    submitForm: function () {
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            this.addLoading = true
            let params = this.buildAddData()
            console.log(params)
            this.$api.worktime.updateWorkTime(params).then((res) => {
              this.addLoading = false
              if(res.code == 200) {
                this.$message({ message: '操作成功', type: 'success' })
                this.dataFormVisible = false
              } else {
                this.$message({message: '操作失败, ' + res.msg, type: 'error'})
              }
            })
          })
    },
  // 构造新增数据
  buildAddData:function()
  {
    var addData = new Array()
    console.log(this.workTimeForm)
    if(this.workTimeForm.workDays.length!=8 || ''==this.workTimeForm.monthTime)
    {
      this.$message({ message: '填写不能为空', type: 'error' })
      return;
    }
    var arrGroup = this.workTimeForm.groups
    var arrDays = this.workTimeForm.workDays
    for(var i=0;i<arrGroup.length;i++)
      { 
        var group = arrGroup[i];
        var workDays = arrDays[i];
        var temp = {"group":group,"workDays":workDays,"pointVal":this.workTimeForm.pointVal,"monthDate":this.workTimeForm.monthTime}
        console.log("********"+temp)
        addData.push(temp)
      }
       return addData
  },
    // 构造更新数据
    buildUpdateData:function()
    {
      // 清空目标容器数组 非常重要
      this.updateData = []

      // {"group":"甲班","workDay":"21.75"}  
      var arrGroup = this.bodyData[0].groups;
      var arrDays = this.bodyData[0].days;
      var point = this.bodyData[0].pointVal[0];
      console.log(this.monthDate)

      for(var i=0;i<arrGroup.length;i++)
      { 
        var group = arrGroup[i];
        var workDays = arrDays[i];
        var temp = {"group":group,"workDays":workDays,"pointVal":point,"monthDate":this.monthDate}
        console.log("********"+temp)
        this.updateData.push(temp)
      }
      console.log("updateData======="+this.updateData)
    },

  // 月工作时间表格后台更新接口/////
   updateWorkTime:function()
   {
    this.buildUpdateData();
       this.$api.worktime.updateWorkTime(this.updateData).then(res=>
      {
        if(200 == res.code)
        {
           this.$message({type: 'success',message: '数据更新成功'});
           // 刷新页面
           this.getGroupWorkTime();
        }else
        {
          this.$message({type: 'error',message: "数据更新失败" });
        }
      })
   },

    // 获取workTime --取当月，没有则取上月，取最近的数据
    // {"id":"1","group":"甲班","workDays":"21.75","pointVal":"50","monthDate":"2019/7/1"},
    getGroupWorkTime:function()
    {
          console.log(this.monthTime)
        this.$api.worktime.findAll({'monthTime':this.monthTime}).then(res=>
        {
          this.groupWorkTime = res.data;
          console.log(res.date)
          if(null==this.groupWorkTime || this.groupWorkTime.length==0)
          {
             this.$message({type: 'error',message: "当月工时数据为空，请先新增" });
             this.bodyData[0] ={ pointVal:[],groups: [], days: []};
          }
          console.log(res.data)
            this.initGroups( this.groupWorkTime);
            this.initWorkDays( this.groupWorkTime);
            this.initMonthDate()
            this.initLastTime()
            this.initPointVal()
        })
    },

    // 初始化月份
      initMonthDate:function()
    {
      if(null == this.groupWorkTime)
      {
        this.monthDate = this.monthTime;
      }

      this.monthDate = this.groupWorkTime[0].monthDate;
      
    },

    // 初始化创建时间
    initLastTime:function()
    {
      this.lastTime = this.groupWorkTime[0].createTime;
      
    },
    // 初始化点值
    initPointVal:function()
    {
      // 清空整个bodyData
      this.bodyData[0].pointVal=[];
      this.bodyData[0].pointVal[0] = this.groupWorkTime[0].pointVal;
      this.pointVal = this.groupWorkTime[0].pointVal;
    },
    // 初始化group
    initGroups:function(groupWorkTime)
    { 
      // 清空整个bodyData
        this.bodyData[0].groups=[];
      for (var i=0;i<groupWorkTime.length;i++) {
        var temp = groupWorkTime[i].group
        if(temp)
        {
          this.bodyData[0].groups.push(temp)
        }
      }
    },
    // 初始化days
    initWorkDays:function(groupWorkTime)
    {
        // 清空整个bodyData
        this.bodyData[0].days=[]
      for (var i=0;i<groupWorkTime.length;i++) {
        var temp = groupWorkTime[i].workDays
        if(temp)
        {
          this.bodyData[0].days.push(temp)
        }
      }
    },

    onBeforeUpload:function()
    {
      // 选择时间
      if(!this.createTime)
      {
        this.$message({message: '上传失败,请先选择生产时间!', type: 'error'})
      }
    },
     customUpload(file) {
      // this.generatorFileMd5(file.file)
      // 自定义上传
      this.$api.fileUpDown.upDayAmtExecl(this.file).then(response => {
        console.log(response)
         if(response.code === 200){
         console.log('解析成功')
          this.$message({message: '上传成功,对应数据生产时间：' + this.createTime ,type: 'success'})
       }else{
          console.log('解析失败')
           this.$message({message: '上传失败, ' + response.msg, type: 'error'})
         }

      })
    },
      
      handlesuccess(response, file){
      //response即为后台返回的全部内容
       if(response.code === 200){
         console.log('解析成功')
        this.$message({message: '上传成功,对应数据生产时间：' + this.createTime ,type: 'success'})
       }else{
          console.log('解析失败')
           this.$message({message: '上传失败, ' + response.msg, type: 'error'})
         }
       },

    editData(data) {
      console.log("编辑所在行的值为", data);
      console.log("bodyData=====", this.bodyData);
    },

    
    },
    created()
    {
      // 查询班组月上班天数
      this.getGroupWorkTime();
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.el-table__expand-column .cell {
  display: none;
}
.el-footer {
    background-color:  #f8f8f8;
    color: #333;
    font-size: x-large;
    text-align:center;
  }


</style>




